
// Hva skjer
// ---------------------------------------------------

// Page header
// ---------------------------
@media (min-width: $lg_break) {
    .page_header_container--hvaskjer {
        // screen-reader-text for Desktop
        clip: rect(1px, 1px, 1px, 1px); 
        height: 1px; 
        width: 1px; 
        overflow: hidden; 
        position: absolute !important;
    }
}

@media (max-width: $lg_break_max) {
    
    .hvaskjer_menu_container {
        @include rem('margin', 0 0 35px);
    }
    
    .toggle_additional_menu_link {
        display: none;
    }
    .hvaskjer_additional_menu {
        padding: 0;
        margin: 0;
        
        li:last-child {
            border-bottom: 0;
        }
    }
}

// Desktop menu
// ---------------------------

@media (min-width: $lg_break) {
    
    .hvaskjer_menu_container {
        @include rem('margin', -19px 0 31px);
    }
    
    .hvaskjer_menu {
        padding: 0;
        margin: 0;

        li {
            display: inline-block;
            list-style-image: none;
            @include rem('margin', 0 8px 4px 0);
            @include rem('border', 1px solid $almond);
            @include border-radius();
            
            &.active {
                background-color: $rum_swizzle;
            }

            a {
                display: block;
                @include rem('font-size', 20px);
                line-height: ( 28px / 20px );
                @include SourceSansProRegular();
                @include rem('padding', 9px 12px);

                &:hover {
                    background-color: $rum_swizzle;
                }
                
                
                &.toggle_additional_menu_link {
                    position: relative;
                    z-index: 30;
                    @include rem('padding-right', 45px);

                    .icon {
                        position: absolute;
                        @include rem('top', 9px);
                        @include rem('right', 5px);
                        background-position: -256px -32px; // .arrow_up_19_blue
                    }
                }
            }
            
            &.expanded {
                @include border-radius($base_border_radius $base_border_radius 0 0);
            }
            
            &.collapsed .toggle_additional_menu_link .icon {
                background-position: -256px 0; // .arrow_down_19_blue
            }
        }
    }
    
    // Ekstra meny container
    .hvaskjer_additional_menu_container {
        position: relative;
    }
    
    // Ekstra meny liste
    .hvaskjer_additional_menu {
        position: absolute;
        z-index: 20;
        @include rem('top', 46px);
        @include rem('left', -1px);
        padding: 0;
        margin: 0;
        @include rem('min-width', 240px);
        background-color: $white;
        @include rem('border', 1px solid $almond);
        @include border-radius(0 $base_border_radius $base_border_radius $base_border_radius);
        
        li {
            display: block;
            border: 0 none;
            margin: 0;
            @include rem('border-bottom', 1px solid $almond);
            @include border-radius(0);
            
            &:last-child {
                border-bottom: 0;
            }
        }
        
        .collapsed & {
            display: none;
        }
    }
}

