
// Organisasjonsenhet
// ---------------------------------------------------


// Menu
// ---------------------------
.org_menu_container {
    @include rem('border', 1px solid $zombie);
    border-top: 0 none;
    @include border-radius(0 0 $base_border_radius $base_border_radius);
    @include rem-box-shadow(0 1px 2px $alto);
    overflow: hidden;

    &--inactive {
        position: relative;
        background-color: $white;
        opacity: .6;

        a {
            pointer-events: none;
            user-select: none;
        }
    }
}

.org_menu_mobile_header {
    display: block;
    color: $link_color;
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    background-color: $rum_swizzle;
    @include rem('padding', 13px 50px 14px 10px);
    position: relative;
    
    &:hover,
    &:active {
        color: $link_color_hover;
        text-decoration: underline;
    }
    
    .icon {
        position: absolute;
        @include rem('top', 13px);
        @include rem('right', 7px);
        background-position: -256px -32px; // .arrow_up_19_blue
        
        .org_menu_collapsed & {
            background-position: -256px 0; // .arrow_down_19_blue
        }
    }

    @media (min-width: $lg_break) {
        display: none;
    }
}

// Toggle Effect
.org_menu_expanded .org_menu_mobile_header {
    @include rem('border-bottom', 1px solid $almond);
    @include rem-box-shadow(0 1px 2px $alto);
}

.org_menu {
    @include hyphens();
    padding: 0;
    margin: 0;
    @include transition($property: opacity);
    
    li {
        list-style: none;
		@include rem('border-bottom', 1px solid $almond);
        
        .org_menu_link {
            display: block;
            @include rem('padding', 10px 0 10px 10px);

            &:hover,
            &:active {
                background-color: $rum_swizzle;
                text-decoration: none;
            }
            &:focus {
                position: relative;
                z-index: 100;
                @include outline($outline-offset: 0);
            }
        }
        
        &.active > a {
            @include SourceSansProBold();
        }
    }
}

// Toggle Effect
.org_menu_collapsed .org_menu {
    height: 0;
    opacity: 0;
}

// Sub Menu
li.with_org_sub_menu {
    position: relative;
    z-index: 0;

    .org_menu_link {
        @include rem('padding-right', 10px);
        @include rem('margin-right', 48px);
        @include rem('border-right', 1px solid $almond);
    }

    // Toggle button
    .org_sub_menu_toggle_link {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        @include rem('height', 47px);
        @include rem('width', 48px);
        background-color: $white;

        .icon {
            display: block;
            overflow: hidden;
            text-indent: -3000em;
            @include rem('margin', 10px 0 0 8px);
            background-position: -288px -32px; // .arrow_up_15_blue
        }
        
        &:hover,
        &:active {
            background-color: $rum_swizzle;
        }
        &:focus {
            z-index: 100;
            @include rem('height', 45px);
        }
    }
    
    ul {
        position: relative;
        z-index: 2;
        padding: 0;
        margin: 0;
        background-color: $rum_swizzle;
        @include rem('border-top', 2px solid $almond);
        @include rem('border-bottom', 1px solid $almond);
        
        li {
            @include rem('margin-left', 25px);
            @include rem('margin-right', 25px);
            border-bottom-color: $white;
            
            .org_sub_menu_link {
                display: block;
                @include rem('padding', 11px 12px 9px 29px);
                @include rem('margin-left', -25px);
                @include rem('margin-right', -25px);

                &:hover,
                &:active {
                    background-color: darken($rum_swizzle, 3);
                    text-decoration: none;
                }
            }
        }

        li:last-child {
            border-bottom: 0 none;
        }
    }
    
    // Collapsed
    &.org_sub_menu_collapsed {
        ul {
            display: none;
        }
        .org_sub_menu_toggle_link .icon {
            background-position: -288px 0; // .arrow_down_15_blue
        }
    }
    
    // Expanded
    &.org_sub_menu_expanded {
        ul {
            display: block;
        }
        .org_sub_menu_toggle_link .icon {
            background-position: -288px -32px; // .arrow_up_15_blue
        }
    }

    @media (min-width: $lg_break) {
        .org_sub_menu_toggle_link .icon {
            @include rem('margin-top', 7px);
        }
        ul li .org_sub_menu_link {
            @include rem('padding-left', 25px);
        }
    }
}


// Reset mobile styling on desktop
@media (min-width: $lg_break) {
    
    .org_menu_container {
        overflow: visible;
        border: 0 none;
        @include box-shadow(none);
    }

    .org_menu_collapsed .org_menu {
        height: auto;
        opacity: 1;
    }
}



// Org main column
// ---------------------------
.org_main_col {
    // Adjust down to match the menu to the left - desktop
    @include rem('margin-top', 10px);
}




// Kontakt
// ---------------------------
.contact_content {
    @include hyphens($mode: none);
    @include clearfix();

    .icon {
        // Hide icons per default
        display: none;
    }
    

    .with_icon {
        position: relative;
        @include rem('padding-left', 30px);

        a {
            text-decoration: none;

            &:hover,
            &:active {
                text-decoration: underline;
            }
        }

        .icon {
            display: inherit;
            position: absolute;
            @include rem('top', -2px);
            @include rem('left', -8px);
        }
    }
}


// Kontakt border box
// ---------------------------
.contact_box {
    @include rem('border', 1px solid $zombie);
    @include border-radius();
    
    .contact_content {
        @include rem('font-size', 18px);
        line-height: ( 21px / 18px );
        @include rem('padding', 21px 14px 7px 28px);
        
        dd {
            @include rem('margin-bottom', 14px);
        }
    

        .with_icon .icon {
            @include rem('top', -5px);
            @include rem('left', -6px);
        }
    }

    // Read more
    .read_more_contact_button {
        display: block;
        @include rem('padding', 9px 28px 10px);
        background-color: $rum_swizzle;
        @include border-radius(0 0 $base_border_radius $base_border_radius);
        .read_more_link {
            @include rem('padding-right', 12px);
            background-position: right 10px;
        }
    }
}




// Static map image container on orgenhet frontpage
// ---------------------------
.contact_box_with_static_image {
    position: relative;
    z-index: 0;
    @include rem('padding-top', 190px);
    
    @media (min-width: $md_break) {
        padding-top: 0;
        @include rem('padding-bottom', 50px);

        .contact_content,
        .read_more_contact_button {
            margin-left: 48%;
        }

        .contact_content {
            @include rem('min-height', 64px);
        }

        .read_more_contact_button {
            position: absolute;
            right: 0;
            left: 0;
            bottom: 0;
            z-index: 2;
            text-align: center;
            @include rem('padding-right', 29px);
            @include border-radius(0 0 $base_border_radius 0);
        }
    }
}
    
// The small map
.contact_static_image_container_small {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    @include rem('height', 190px);

    .contact_static_image_holder {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-position: center center;
        background-size: cover;
        background-color: $janna;
        @include border-radius($base_border_radius $base_border_radius 0 0);

        &.desktop {
            display: none;
        }
    }

    .contact_static_image_link {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;

        &:hover .contact_static_image_link_text,
        &:active .contact_static_image_link_text,
        &:focus .contact_static_image_link_text {
            text-decoration: underline;
            background-color: $white;
            @include rem-box-shadow(0 1px 2px rgba($black, 18/100));
        }

        .contact_static_image_link_text {
            position: absolute;
            z-index: 3;
            top: 0;
            @include rem('left', 22px);
            @include rem('padding', 9px 12px);
            @include rem('font-size', 16px);
            line-height: ( 21px / 16px );
            background-color: rgba($white, 97/100);
            @include border-radius(0 0 $base_border_radius $base_border_radius);
            @include rem-box-shadow(0 1px 2px rgba($black, 8/100));
        }
    }

    // Small contact box
    .contact_static_image_address {
        position: absolute;
        z-index: 3;
        @include rem('right', 24px);
        @include rem('bottom', 14px);
        @include rem('left', 24px);
        text-align: left;
        color: $black;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        margin: 0;

        dd {
            display: inline-block;
            margin: 0;
            text-shadow: 0 0 2px $white;
            @include rem('padding', 3px 12px 4px);
            background-color: rgba($white, 97/100);
            @include border-radius();
        }
    }
    
    // Tablet & desktop
    @media (min-width: $md_break) {
        right: auto;
        height: auto;
        bottom: 0;
        @include calc(width, "48%");

        .contact_static_image_holder {
            @include border-radius($base_border_radius 0 0 $base_border_radius);
        }
    }
    
    @media (min-width: $lg_break) {
        
        .contact_static_image_holder.mobile {
            display: none;
        }
        .contact_static_image_holder.desktop {
            display: inherit;
        }
    }
}



// Static map image container on orgenhet contact page
// ---------------------------
.contact_page_with_static_image {
    position: relative;
    z-index: 0;
    @include rem('margin-bottom', 32px);
    
    // The large map
    .contact_page_static_image {
        display: block;
        width: 100%;
        background-color: $janna;
        @include rem('min-height', 100px);
        @include rem('border', 1px solid $zombie);
        @include border-radius();
        
        &.desktop {
            display: none;
        }
    
        @media (min-width: $md_break) {

            &.mobile {
                display: none;
            }
            &.desktop {
                display: inherit;
            }
        }
    }

    .contact_static_image_link {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;

        &:hover .contact_static_image_link_text,
        &:active .contact_static_image_link_text,
        &:focus .contact_static_image_link_text {
            text-decoration: underline;
            background-color: $white;
            @include rem-box-shadow(0 1px 2px rgba($black, 18/100));
        }

        .contact_static_image_link_text {
            position: absolute;
            z-index: 3;
            @include rem('top', 1px);
            @include rem('left', 25px);
            @include rem('padding', 6px 19px 7px 15px);
            background-color: rgba($white, 95/100);
            @include border-radius(0 0 $base_border_radius $base_border_radius);
            @include rem-box-shadow(0 1px 2px rgba($black, 8/100));
        }
    }

    // Contact page
    .contact_static_image_address {
        position: absolute;
        z-index: 3;
        @include rem('right', 25px);
        @include rem('bottom', 14px);
        @include rem('left', 25px);
        text-align: left;
        color: $black;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
        margin: 0;
        
        dd {
            display: inline-block;
            margin: 0;
            text-shadow: 0 0 2px $white;
            @include rem('padding', 3px 12px 4px);
            background-color: rgba($white, 97/100);
            @include border-radius();
        }
    }
        
    @media (min-width: $md_break) {
        .contact_static_image_address {
            @include rem('top', 10px);
            @include rem('right', 14px);
            bottom: inherit;
            @include rem('left', 200px);
            text-align: right;
        }
    }
}



// Employee
// ---------------------------
.employee_name {
    @include rem('font-size', 22px);
    line-height: ( 28px / 22px );
    @include SourceSansProRegular();
    margin: 0;
}

.employee_work_description {
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
}

.contact_content_employee_dl {
    @include rem('padding-bottom', 14px);
    @include rem('margin-bottom', 14px);
    @include rem('border-bottom', 1px solid $zombie);
    
    dd {
        @include rem('margin-bottom', 2px);
    }
}





a.itslearning_link {
    display: inline-block;
    text-decoration: none;
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );

     .itslearning_logo {
        display: inline-block;
        @include rem('height', 26px);
        @include rem('width', 80px);
        background: url(../images/itslearning_logo.svg) no-repeat -8px -3px transparent;
        vertical-align: text-bottom;
    }

    .schedules_itslearning_link_text {
        display: inline-block;
        @include rem('padding-left', 12px);
        background: url(../images/default_list_style_image.svg) no-repeat 3px 8px transparent;
    }
    &:hover .schedules_itslearning_link_text,
    &:active .schedules_itslearning_link_text,
    &:focus .schedules_itslearning_link_text {
        text-decoration: underline;
    }
}




// Important_link
// -------------------------------------------
.important_link {
    display: block;
    position: relative;
    @include rem('padding', 10px 24px 11px 44px);
    text-decoration: underline;
    color: $white;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    text-shadow: 0 1px 2px $link_color;
    background: url(../images/blue_arrow_bg.svg) no-repeat center right $danube;
    background-size: cover;
    
    &:before {
        position: absolute;
        @include rem('top', 9px);
        @include rem('left', 5px);
        content: "";
        @include rem('width', 32px);
        @include rem('height', 32px);
        background: url($main_sprite_url) no-repeat -160px -161px transparent; // .calendar_icon
    }
    
    &:hover {
        color: $white;
    }
}

// Skoleruten
// -------------------------------------------
.toggle_holidays_link {
    display: block;
    // Tablet & desktop
    @media (min-width: 40em) {
        display: inherit;
    }
}

.holidays_container {
    @include rem('border', 1px solid $zombie);
    @include rem('margin-bottom', 16px);
    @include border-radius();
    @include clearfix();
    
    .holidays_header {
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include SourceSansProRegular();
        background-color: $rum_swizzle;
        margin: 0;
        @include rem('padding', 7px 15px 6px);
        @include rem('border-bottom', 1px solid $zombie);
        @include border-radius($base_border_radius $base_border_radius 0 0);
    }
    .holidays_list {
        padding: 0;
        margin: 0;
        display: table;
        border-collapse: collapse;
        width: 100%;
    
        .holidays_list_item {
            display: table-row;
            list-style-image: none;
            @include rem('border-bottom', 1px solid $zombie);
            
            &:last-child {
                border-bottom: 0 none;
            }
            
            .holiday_date {
                display: table-cell;
                width: 34%;
                @include rem('padding', 12px 16px 13px 16px);
                @include rem('border-right', 1px solid $zombie);
                @include box-sizing();

                &__date{
                    display: block;
                    clear: both;
                    @media (min-width: 36em) {
                        display: inline;
                        padding-right: 10px;
                    }
                }

                &__time{
                    float: left;
                    @media (min-width: 36em){
                        float: right;
                    }
                }
            }
            
            .holiday_title_container {
                display: table-cell;
                @include rem('padding', 12px 16px 13px);
            }
            
            &.hidden {
                display: none;
            }
        }

        .meeting_title_link {
            text-decoration: underline;
        }
        
        .holiday_additional_info_list {
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
            margin-bottom: 0;
            @include rem('margin-top', 7px);
            @include rem('padding-left', 16px);
        }
    }
    // Tablet & desktop
    @media (min-width: 36em) {
        .holidays_list .holidays_list_item .holiday_date {
            border-right: 0 none;
            @include rem('padding', 12px 0 13px 16px);
            @include rem('width', 135px);
        }
    }
}

// Arbeidsplaner - tout - liten boks
// -------------------------------------------
.schedules_tout {
    
    .tout_header {
        @include rem('border-bottom', 2px solid $zombie);
        @include rem('margin-bottom', 12px);
        
        a {
            color: $black;
        }
    }
    
    .tout_excerpt a {
        text-decoration: none;
        
        &:hover,
        &:active {
            text-decoration: underline;
        }
    }
}




// Skoleruten - tout - liten boks
// -------------------------------------------
.holiday_tout {
    
    .tout_header {
        @include rem('border-bottom', 2px solid $zombie);
        @include rem('margin-bottom', 12px);
        
        a {
            color: $black;
        }
    }
    
    ul.holiday_additional_info_list {
        @include rem('margin', 4px 0 0 14px);
        
        li {
            background-position: 1px 12px;
            @include rem('padding', 2px 0 2px 14px);
            border-bottom: 0 none;
        }
    }
    
    .holiday_link_container {
        @include rem('border-top', 1px solid $zombie);
        @include rem('margin-top', 12px);
        @include rem('padding-top', 10px);
        
        a {
            text-decoration: none;

            &:hover,
            &:active {
                text-decoration: underline;
            }
        }
    }
}



// Arrangement oversikt
// ---------------------------
.event_image_container {
    @include rem('margin-bottom', 6px);
    
    .event_image_link {
        display: block;
        
        .event_image {
            display: block;
            width: 100%;
        }
    }
}

.event_story_container {    
    .event_story_title {
        margin: 0;
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include SourceSansProRegular();
    }
    .event_story_date {
        margin-bottom: 0;
    }
    .art_event_link {
        @include rem('margin-top', 16px);
        margin-bottom: 0;
    }
}

// Margin bottom
.inner_event_container {
    @include rem('margin-bottom', 40px);
}
@media (max-width: $md_break_max) {
    .outer_event_container .row_no_clearfix div:last-child .inner_event_container {
        margin-bottom: 0;
    }
}
@media (min-width: $md_break) {
    .outer_event_container .row_no_clearfix .col_lg_6:last-child .inner_event_container,
    .outer_event_container .row_no_clearfix .col_lg_6:nth-last-child(2) .inner_event_container {
        margin-bottom: 0;
    }
}


@media (min-width: $md_break) {
    
    // Odd - change first element
    .col_lg_12 .inner_event_container {
        display: inline-block;
        width: 100%;
        @include rem('border-bottom', 1px solid $janna);
        @include clearfix();
        @include rem('margin-bottom', 24px);
        @include rem('padding-bottom', 24px);

        .event_image_container {
            margin-bottom: 0;
            @include calc(width, "50% - .9375rem"); // -15px
            float: left;
        }
        .event_story_container {
            @include calc(width, "50% - .9375rem"); // -15px
            float: right;
        }
    }
    
    // Remove border bottom from top item if just 1 item
    .outer_event_container .row_no_clearfix .col_lg_12:last-child .inner_event_container {
        border-bottom: 0;
        padding-bottom: 0;
        margin-bottom: 0;
    }
}



// Read more link
.event_read_more_link_container {    
    @include rem('border-top', 1px solid $janna);
    @include rem('padding-top', 12px);
}
.outer_event_container + .event_read_more_link_container {
    @include rem('margin-top', 24px);
}




