.tabs {
    list-style: none;
    padding: 0;
    margin: 0;
    @include rem('border-bottom', 1px solid $link_color);
    @include clearfix();

    &__tab {
        float: left;
        display: inline-block;
        list-style-image: none;
        @include rem('border', 1px solid transparent);
        @include border-radius($base_border_radius $base_border_radius 0 0);
        background-color: $white_sand;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('margin-left', 4px);

        & a {
            display: block;
            color: $link_color_hover;
            @include rem('padding', 7px 7px 6px);
            
            .hidden_on_small_screens {
                display: none;
            }
        }
        
        &--active {
            position: relative;
            border-color: ($link_color $link_color $white);
            background-color: $white;
            @include rem('bottom', -1px);

            &:before,            
            &:after {
                position: absolute;
                @include rem('bottom', -1px);
                z-index: 2;
                @include rem('width', 3px);
                @include rem('height', 3px);
                @include box-sizing();
                background-color: $white;
                content: '';
            }

            &:before {
                @include rem('left', -3px);
            }

            &:after {
                @include rem('right', -3px);
            }

            & a {
                position: relative;
                color: $black;

                &:before,            
                &:after {
                    position: absolute;
                    @include rem('bottom', -1px);
                    z-index: 3;
                    @include rem('width', 3px);
                    @include rem('height', 3px);
                    @include box-sizing();
                    @include rem('border-bottom', 1px solid $link_color);
                    content: '';
                }

                &:before {
                    @include rem('left', -3px);
                    @include rem('border-right', 1px solid $link_color);
                    @include border-radius(0 0 $base_border_radius 0);
                }

                &:after {
                    @include rem('right', -3px);
                    @include rem('border-left', 1px solid $link_color);
                    @include border-radius(0 0 0 $base_border_radius);
                }
            }
        }        
        
        @media (min-width: 375px) {

            & a {
                @include rem('padding', 7px 16px 6px);
            }
        }
        
        @media (min-width: 550px) {
            @include rem('margin-left', 6px);

            & a .hidden_on_small_screens {
                display: inline;
            }
        }
    }
}