
// Din side - gammel side som skal få noe ruu-styling i en overgangsperiode fra september 2016
// -------------------------------------------------------------------------------------------

.dinside.trekkspillmeny {
    @include rem('margin', 0 0 60px);
    clear: both;
    
    .border {
        background-color: $rum_swizzle;
        @include rem('border', 1px solid $zombie);
        @include border-radius();
    }
    
    ul {
        margin: 0;
        padding: 0;
        
        li {
            list-style: none;
        }
    }
    
    h2 {
        margin-top: 0;
        @include rem('border-bottom', 2px solid $zombie);
        @include rem('padding-bottom', 5px);
        @include rem('margin-bottom', 32px);

        a {
            color: $black;
        }
    }
    
    h3 {
        margin-top: 0;
        @include rem('border-bottom', 1px solid $zombie);
        @include rem('padding-bottom', 5px);
        @include rem('margin-bottom', 32px);

        a {
            color: $black;
        }
    }
    
    .error {
        @include rem('padding', 12px 12px 12px 44px);
        @include rem('margin', 0 0 12px);
        @include rem('border', 1px dashed $brand_danger);
        background: transparent url('../images/warning.gif') no-repeat 10px 12px;
        @include border-radius();
    }
    
    .red {
        color: $brand_danger;
    }
    
    ul.niva_1 {
        @include rem('padding', 0 10px);
        
        & > div {
            @include rem('border-bottom', 1px solid lighten($zombie, 8));
            
            &:last-child {
                border-bottom: 0 none;
            }
        }
        
        li {
            margin: 0;
            padding: 0;
            @include rem('min-height', 25px);
            
            a,
            .mappetittel {
                display: block;
                text-decoration: none;
                cursor: pointer;
                @include rem('padding', 12px 32px 12px 5px);
                @include SourceSansProRegular();
                @include rem('font-size', 20px);
                line-height: ( 28px / 20px );
                background: transparent url('../images/icon_tjenestePlus.gif') no-repeat 99% 18px;
            
                &:hover > div:first-child {
                    text-decoration: underline;
                }
                
                .metainfo {
                    @include rem('font-size', 18px);
                    line-height: ( 28px / 18px );
                }
            }
            
            &.active a {
                background-color: $white;
                background-image: url('../images/icon_tjenesteMinus.gif');
                background-position: 99% 13px;
                @include rem('padding-top', 7px);
                @include rem('border-top', 5px solid $rum_swizzle);
                @include rem('border-bottom', 1px solid lighten($zombie, 8));
            }
            
            &#empty {
                a,
                .mappetittel {
                    cursor: text;
                    background-image: none;
                }
            }
            
            &#visalle,
            &#visflere {

                a,
                .mappetittel {
                    background-image: none;
                    @include rem('padding-bottom', 14px);
                }
            }
        }
        
        // Loading style
        li.empty {
            @include rem('margin', 12px 0);
            @include rem('padding', 12px);
            @include border-radius();
            
            p {
                margin-bottom: 0;
            }
        }
    }
    
    ul.niva_2 {
        background-color: $white;
        @include rem('border-bottom', 8px solid $rum_swizzle);
        @include rem('padding', 12px 10px 12px 5px);
        
        li {
            border-bottom: 1px dashed lighten($zombie, 8);
            @include rem('padding', 8px 0 8px 5px);
            
            a {
                @include rem('font-size', 18px);
                line-height: ( 28px / 18px );
                @include rem('min-height', 20px);
                display: inline;
                border: 0 none !important;
                text-decoration: underline;
                
                &.file {
                    background: transparent url('../images/docIcon.gif') no-repeat left 12px;
                    @include rem('padding-left', 22px);
                }
            }
        }
    }
    
    .dokumentdato {
        float: right;
        color: $grey7;
        @include rem('margin-left', 10px);
    }
    
    .show_more_braarkiv {
        border-bottom: 0 none;
        
        li#visflere {
            @include rem('border-bottom', 1px solid lighten($zombie, 8));
        }
    }
    
    .show_all_braarkiv {
        border-bottom: 0 none;
        
    }
    
    ul.niva_1 {
        
        & > div {
            
            &:last-child {
                border-bottom: 0 none;
            }
        }
    }
}





// Din side - Dokumenter
// -------------------------------------------------------------------------
.trekkspillmeny .dokument {
    @include rem('padding', 16px 15px);
    
    h4 {
        @include rem('margin-bottom', 16px);
    }
    
    ul.filer {
        background-color: $white;
        @include rem('padding', 12px 18px 0);
        @include rem('margin', 0 0 24px);
        @include rem('border', 1px dashed #E3DAC2);
        @include border-radius();
    }
    
    li.fil {
        border-bottom: 1px dashed lighten($zombie, 8);
        @include rem('padding-bottom', 6px);
        @include rem('margin-bottom', 12px);
        
        a {
            background: transparent url('../htdocs/images/icon_pdf.gif') no-repeat 0 4px;
            @include rem('padding-left', 24px);
        }
    }
    
    .meta {
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
    }
}














