// HTML Standard elements
// -----------------------------------------------------------------------------

// 1. Set default font family to sans-serif.
// 2. Prevent iOS text size adjust after orientation change, without disabling user zoom.

html {
  font-family: sans-serif; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
  font-size: $font_size_base; // Must be set to default 16px because of all the sprites
}

body,
button,
input,
select,
textarea {
    color: $text_color;
    font-family: 'Source Sans Pro', Verdana, sans-serif;
    @include SourceSansProRegular();
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    // 28 / 19 = 1.47368421 (28px) // Unit-less `line-height` // 19 is body-font-size
}

.vr {
    padding-top: 0;
    padding-bottom: 24px;
    // Demonstrate the vertical rhythm at 28px line height)
    background-image: linear-gradient(#ddd 1px, transparent 1px);
    background-size: 100% 7px; // 7px / 14px / 21px / 28px / 35px / 42px / 49px
    // max-width: 600px; // Body-tekst maks-bredde er 600px
    // margin: 0 auto;
    
    * {
        background-color: transparent !important;
    }
}

.spacing{
  .mbs {
    @include rem('margin-bottom', 7px);
    box-shadow: 0px 7px 0px rgba(46, 204, 64, 0.5); //green
    &:after{
      content: "mbs 7px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);

    }
  }

  .mbm {
    @include rem('margin-bottom', 14px);
    box-shadow: 0px 14px 0px rgba(61, 153, 112, 0.5); //olive
    &:after{
      content: "mbm 14px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);

    }
  }

  .mbml {
    @include rem('margin-bottom', 21px);
    box-shadow: 0px 21px 0px rgba(0, 31, 63, 0.5); //navy
    &:after{
      content: "mbml 21px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);

    }
  }

  .mbl {
    @include rem('margin-bottom', 28px);
    box-shadow: 0px 28px 0px rgba(255, 133, 27, 0.5); //orange
    &:after{
      content: "mbl 28px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);

    }
  }

  .mbxl {
    @include rem('margin-bottom', 35px);
    box-shadow: 0px 35px 0px rgba(255, 220, 0, 0.5); //yellow
    &:after{
      content: "mbxl 35px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);
    }
  }

  .mbxxl {
    @include rem('margin-bottom', 42px);
    box-shadow: 0px 42px 0px rgba(133, 20, 75, 0.5); //maroon
    &:after{
      content: "mbxxl 42px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);
    }
  }

  .mbj {
    @include rem('margin-bottom', 56px);
    box-shadow: 0px 56px 0px rgba(255, 65, 54, 0.8); //red
    &:after{
      content: "mbj 56px";
      position: absolute;
      right: 0;
      color: $brand_danger;
      @include rem('font-size', 12px);
    }
  }
}

body {
  margin: 0;
  position: relative;
  background-color: $white;
  @include rem('min-height', 660px);
}

// Links
// -------------------------------------

a {
  color: $link_color;
  text-decoration: none;
  // Remove the gray background color from active links in IE 10.
  background: transparent;
  
  &:focus {
      @include outline();
  }
  
  &:hover,
  &:active {
    color: $link_color_hover;
  	text-decoration: underline;
  }
}

p a {
  text-decoration: underline;
}


// Headlines
// -------------------------------------

h1,
h2,
h3,
h4,
h5,
h6 {
  // clear: both;
  // We should remove this, if not, image-floating is difficult
}

h1,
h3 {
    @include SourceSansProRegular();
}

h2,
h4,
h5,
h6 {
    @include SourceSansProSemibold();
}

h1 {
    @include rem('font-size', 30px);
    line-height: ( 35px / 30px );
    @include rem('margin', 28px 0 7px);
}

h2 {
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include rem('margin', 34px 0 1px);
}

h3 {
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include rem('margin', 34px 0 1px);
}

h4 {
    @include rem('font-size', 21px);
    line-height: ( 28px / 21px );
    @include rem('margin', 28px 0 0);
}

h5 {
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    @include rem('margin', 28px 0 0);
}

h6 {
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    @include rem('margin', 28px 0 0);
}

.accordion_content,
.accordion_content_editable {
    h3,
    h4,
    h5 {
        margin-top: 0;
    }
}


// Blockquote
// -------------------------------------

blockquote,
q {
  quotes: none;
  @include hyphens($mode: none);
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

blockquote {
    @include rem('margin', 35px 24px);

    p {
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include SourceSansProRegularItalic();
    }
}

blockquote cite,
blockquote small {
    @include rem('font-size', 16px);
    line-height: ( 21px / 16px );
}

blockquote strong,
blockquote b {
    @include SourceSansProRegularItalic();
}


// Other html elements
// -------------------------------------

address {
    @include rem('margin-bottom', 24px);
}

abbr,
acronym {
    letter-spacing: .1em;
    text-transform: uppercase;
    @include rem('font-size', 13px);
    line-height: ( 21px / 13px );
}

abbr[title] {
    cursor: help;
}

b,
strong,
.strong {
    @include SourceSansProSemibold();
    
    cite,
    dfn,
    em,
    i {
        @include SourceSansProSemibold();
    }
}

cite,
dfn,
em,
i {
    @include SourceSansProRegularItalic();
    
    b,
    strong,
    .strong {
        @include SourceSansProRegularItalic();
    }
}  

dfn {
    @include rem('border-bottom', 1px solid $alto);
}

mark,
ins {
    color: $text_color;
    background: #fff9c0;
    text-decoration: none;
    @include rem('padding-left', 4px);
    @include rem('padding-right', 4px);
}

p {
    margin-top: 0;
    @include rem('margin-bottom', 28px);
}

// Fix for the wbr-tag: http://stackoverflow.com/questions/23756932/wbr-tag-doesnt-work-in-ie
wbr:after {
    content: "\00200B";
}

code,
kbd,
tt,
var,
samp,
pre {
  font-family: monospace, serif;
  @include rem('font-size', 13px);
  @include hyphens($mode: none);
}

pre {
  line-height: ( 24px / 13px );
  @include rem('border', 1px solid $alto);
  max-width: 100%;
  overflow: auto;
  white-space: pre;
  white-space: pre-wrap;
  @include hyphens();
  @include rem('padding', 12px 12px 9px);
  @include rem('margin-bottom', 24px);
  @include box-sizing();
}

small,
.small {
  @include rem('font-size', 17px);
  line-height: ( 21px / 17px );
}

big,
.large {
  @include rem('font-size', 22px);
  line-height: ( 28px / 22px );
}

sup,
sub {
  @include rem('font-size', 13px);
  height: 0;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  bottom: 1ex;
}

sub {
  top: .5ex;
}

.highlight {
  background: #fff9c0;
  @include rem('padding-left', 4px);
  @include rem('padding-right', 4px);
}

.error {
  color: $brand_danger !important;
}

.error-text {
  color: $brand_danger !important;
  @include rem('font-size', 17px);
  line-height: ( 21px / 17px );
}

.error-input,
.error-input:focus {
  @include rem('border', 1px solid $brand_danger);
}

hr {
  background-color: $alto;
  border: 0;
  height: 1px;
  @include rem('margin-bottom', 23px);
  
  // Address differences between Firefox and other browsers.
  @include box-sizing(content-box);
}

.lowercase {
    text-transform: lowercase;
}


// Embeds
// 
// Make sure embeds and iframes fit their containers
// -------------------------------------

embed,
iframe,
object,
video {
  margin-bottom: 24px;
  max-width: 100%;
}

p > embed,
p > iframe,
p > object,
span > embed,
span > iframe,
span > object {
  margin-bottom: 0;
}

.embed_container,
.iframe_container,
.object_container,
.video_container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  width: 100%;
  
  embed,
  iframe,
  object,
  video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}


// Lists
// -------------------------------------

ul,
ol {
    @include rem('margin', 0 0 28px);
}

ul {
    list-style: none;
    @include rem('padding-left', 25px);
        
    // Firefox align the list-items 3px wrong
    @-moz-document url-prefix() {
        @include rem('padding-left', 28px);
    }
    
    li {
        list-style-image: url(#{$image-path}/default_list_style_image.svg);
    }
}

ol {
    list-style: decimal;
    @include rem('padding-left', 30px);
}

li > ul,
li > ol {
    @include rem('margin', 0 0 14px);
}

dl {
    margin: 0;
    
    a {
        text-decoration: underline;
    }
}

dt {
    @include SourceSansProSemibold();
}

dd {
    @include rem('margin', 0 0 28px);
}

// Special list's
.service_description,
.accordion_content {  
    ul {
        @include rem('margin-left', 18px);
        li {
            @include rem('margin-bottom', 7px);
        }
        a {
            text-decoration: underline;
        }
    }  
}

// Images
// -------------------------------------

img {
  -ms-interpolation-mode: bicubic;
  vertical-align: middle;
  // Remove border when inside `a` element in IE 8/9/10.
  border: 0;
}


// Responsive images.
.content img {
  max-width: 100%;
}

// Make sure images with height and width attributes are scaled correctly.
.content img[height] {
  height: auto;
}

// Alignment
.align_left {
  float: left;
}

.align_right {
  float: right;
}

.align_center {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.text_align_center {
  text-align: center;
}

.text_align_right {
  text-align: right;
}
 
.margin_left {
  @include rem('margin-left', 12px);
}

.margin_right {
  @include rem('margin-right', 12px);
}


// Figure element
// -------------------------------------

figure {
    // Reset browser defaults of the figure element
    margin: 0;

    // Reset float & aligning on mobile
    @media (max-width: $md_break_max) {
        &.align_left,
        &.align_right {
            float: inherit;
        }
        &.align_left,
        &.align_center,
        &.align_right {
            @include rem('margin', 0 0 28px);
        }
    }
    
    @media (min-width: $md_break) {
        @include rem('margin', 7px 0 0);
        
        &.align_left {
            width: 49%;
            @include rem('margin-right', 30px);
            @include rem('margin-bottom', 21px);
        }
        &.align_center {
            @include rem('margin-bottom', 28px);
        }
        &.align_right {
            width: 49%;
            @include rem('margin-left', 30px);
            @include rem('margin-bottom', 21px);
        }
    }

    img {
        display: block;
        background-color: $white_sand;
        width: 100%;
    }
}

.article__figure-image {
  // Alle bilder i artikkelen vises med lightbox-ikon // min-height gjør at det blir synlig selv om bilde-src feiler
  @include rem('min-height', 40px);
}

figcaption {
    color: $emperor;
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
    @include rem('padding-top', 2px);
  
    p {
        margin-bottom: 0;
    }
}


// Embedded content
// -------------------------------------


svg:not(:root) {
  // Correct overflow not hidden in IE 9/10/11.
  overflow: hidden;
}



// Selection
// 
// Support a widely-adopted but non-standard selector for text selection styles
// to achieve a better experience. See http://core.trac.wordpress.org/ticket/25898.
// -------------------------------------
::selection {
  background: $text_selection;
  text-shadow: none;
}

::-moz-selection {
  background: $text_selection;
  text-shadow: none;
}

// Content max width
// -------------------------------------
.content_max_width {
    @include rem('max-width', 600px);
}

// Skiplinks
// -------------------------------------

.skiplink {
  position: absolute;
  left: -9999em;
  
  &:focus {
    position: static;
    outline: none;
    text-decoration: underline;
  }
}

#skiplink_container {
  background: $link_color;
  
  .skiplink {
    color: $white;
    display: block;
    @include rem('padding', 12px 0);
  }
}


// Table
// -------------------------------------

table,
th,
td {
    border-color: $alto;
}

table {
    width: 100%;
    @include rem('margin', 32px 0 24px);
    border-collapse: collapse;
    border-spacing: 0;

    caption {
        padding: 0;
        text-align: left;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('margin', 0 0 14px);
    }
    
    th,
    td {
        text-align: left;
        vertical-align: top;
        @include rem('padding', 5px 8px 6px);
    }

    th {
        @include SourceSansProRegular();
    }
}

.table_border_style,
.table_border_style th,
.table_border_style td {
    border-style: solid;
}

.table_border_style {
    @include rem('border-width', 1px 0 0 1px);

    th {
        @include rem('border-width', 0 1px 1px 0);
    }

    td {
        @include rem('border-width', 0 1px 1px 0);
    }
}


// Form
// -------------------------------------

fieldset {
    border: none;
    margin: 0;
    padding: 0;
}

legend {
    color: $text_color;
    white-space: normal;
    @include rem('padding', 0 12px);
    // Correct `color` not being inherited in IE 8/9/10/11.
    border: 0;
    padding: 0;
}

label {
    color: $text_color;
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    display: inline-block;
    @include rem('margin-bottom', 7px);
}

button,
.button,
input,
select,
textarea {
    max-width: 100%;
    vertical-align: baseline;
    @include box-sizing();
    @include rem('margin-bottom', 21px);

    &:focus{
      @include outline();
    }

}

input,
textarea {
  background-image: -webkit-linear-gradient(hsla(0,0%,100%,0), hsla(0,0%,100%,0)); // Removing the inner shadow, rounded corners on iOS inputs
  @include rem('border', 1px solid $silver);
  color: $black;
  @include rem('padding', 8px 10px 7px 12px);
  @include border-radius(3px);
  @include rem-box-shadow(0 1px 2px $alto inset);
  
  &.has_error {
    border-color: $brand_danger !important;
    -webkit-box-shadow: 0 1px 2px rgba($brand_danger, 20/100) inset !important;
       -moz-box-shadow: 0 1px 2px rgba($brand_danger, 20/100) inset !important;
            box-shadow: 0 1px 2px rgba($brand_danger, 20/100) inset !important;
  }
  
  &.has_success {
    border-color: $brand_success !important;
    -webkit-box-shadow: 0 1px 2px rgba($brand_success, 20/100) inset !important;
       -moz-box-shadow: 0 1px 2px rgba($brand_success, 20/100) inset !important;
            box-shadow: 0 1px 2px rgba($brand_success, 20/100) inset !important;
  }
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
input[disabled],
.button.disabled {
  // Re-set default cursor for disabled elements.
  cursor: default !important;
  color: $emperor !important;
  text-decoration: none !important;
  background-color: $white_sand !important;
  border-color: $silver !important;
  opacity: .85;
  
  &.positive .add_icon {
    background-position: -359px -259px !important;
  }
}

input[type="checkbox"],
input[type="radio"] {
    padding: 0;
    @include rem('margin', 0 4px 0 0);

    /*

    @include rem('width', 16px);

    border: 0 none;

    */
}

input[type="radio"] {
    @include border-radius(50%);
}

// Checkbox and radio with labels

input[type="checkbox"] + label,
input[type="radio"] + label {
    display: inline;
}



input[type="search"],
input[type="search"]:focus {
    color: $link_color;
    @include rem('text-indent', 4px);
    -webkit-appearance: textfield;
    @include SourceSansProRegularItalic();
    @include border-radius();
}

input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

select {
  @include rem('margin-bottom', 14px);
}

textarea {
  @include rem('padding', 4px 10px 6px);
  vertical-align: top;
  width: 100%;
  // Remove default vertical scrollbar in IE 8/9/10/11.
  overflow: auto;
}

input:focus,
textarea:focus {
  @include rem('border', 1px solid $link_color);
  @include rem-box-shadow(0 1px 2px rgba($link_color, 30/100) inset);
}


// Don't inherit the `font-weight` (applied by a rule above).
// NOTE: the default cannot safely be changed in Chrome and Safari on OS X.
optgroup {
  @include SourceSansProRegular();
}

// Buttons
// -------------------------------------

button,
.button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
    background-color: $white;
    @include rem('border', 1px solid $link_color);
    color: $link_color;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include border-radius();
    @include rem('padding', 7px 13px);
    box-shadow: none;
    text-decoration: none;
  
    &:focus {
        text-decoration: none;
        @include outline();
    }
  
    &:hover,
    &:active {
        outline: 0 none;
        color: $white;
        border-color: $link_color;
        background-color: $link_color;
        text-decoration: none !important;
    }
}

.button {
    display: inline-block;
    text-decoration: none;

    &:active {
        text-decoration: underline;
    }
}

button.active,
.button.active {
    color: $white;
    border-color: $link_color;
    background-color: $link_color;

    &:hover,
    &:active {
        background-color: darken($link_color, 5);
    }
}

button.important,
.button.important {
    color: $white;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include rem('padding-left', 32px);
    background: url(../images/list_arrows_white.svg) no-repeat 12px 0 $link_color;
    border-color: $form_main_color;
    
    &:hover,
    &:active {
        background-color: darken($link_color, 5);
    }
}



// Placeholder text color -- selectors need to be separate to work.

::-webkit-input-placeholder {
  color: $silver;
}

:-moz-placeholder {
  color: $silver;
}

::-moz-placeholder {
  color: $silver;
  opacity: 1; // Since FF19 lowers the opacity of the placeholder by default
}

:-ms-input-placeholder {
  color: $silver;
}



// Hide elements
// -------------------------------------
.hidden {
  display: none;
}


// Assistive text
// -------------------------------------
.screen-reader-text {
    clip: rect(1px, 1px, 1px, 1px); 
    height: 1px; 
    width: 1px; 
    overflow: hidden; 
    position: absolute !important;
}