// Escenic - en enkel artikler
// ----------------------------------------------------------


// Finne gamle artikler og sidemaler via siteimprove
// ------------------------------------
.ikkeibruk {
    color: $white;
    background-color: $white;
    clip: rect(1px, 1px, 1px, 1px); 
    height: 1px; 
    width: 1px; 
    overflow: hidden; 
    position: absolute !important;
}


// Stikktittel
// ------------------------------------
.teaser_kicker {
    text-transform: uppercase;
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
    @include rem('margin', 4px 0 3px);
}


// Main title
// ------------------------------------
h1.article_header {
    color: $black;
    @include rem('font-size', 26px);
    line-height: ( 28px / 26px );
    @include rem('min-height', 28px);
    @include hyphens();
    @include rem('border-bottom', 2px solid $zombie);
    @include rem('padding-bottom', 2px);
    @include rem('margin', 0 0 19px);
    
    @media only screen and (min-width: $md_break) {
        @include rem('font-size', 30px);
        line-height: ( 35px / 30px );
        @include rem('margin-top', -5px);
    }
}


// Ingress
// ------------------------------------
#newsDetail .ingress {
    @include SourceSansProSemibold();
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    @include rem('max-width', 600px);
    @include rem('margin-bottom', 21px);
}

// Article body title
// ------------------------------------
.article_body_content {
    @include clearfix();
    @include rem('margin-bottom', 28px);
    @include rem('max-width', 600px);
    
    img {
        max-width: 100%;
    }
    
    ul li a {
        text-decoration: underline;
    }
}



// Event info - standardpf
// ------------------------------------
.art_event_date {
    display: block;
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    @include rem('margin-bottom', 14px); 
}
.art_event_link {
    @include rem('font-size', 22px);
    line-height: ( 28px / 22px );
    @include rem('margin', 0 0 28px);
    position: relative;
}
.art_event_date + .art_event_link {
    //position: relative;
    //@include rem('top', -12px);
    //@include rem('margin-bottom', 12px); 
}

// Inside kolonnemal
.moduleStory .art_event_link {
    @include rem('margin-bottom', 14px); 
}

// Bilde i tabell i brødtekst
// ------------------------------------
#newsDetail .module table img {
    display: inline-block;
    max-width: 100%;
}


// General related content box styling
// ------------------------------------

.related_content_header {
    @include SourceSansProSemibold();
    @include rem('font-size', 21px);
    line-height: ( 28px / 21px );
    @include rem('margin-bottom', 12px);
    @include rem('border-bottom', 2px solid $zombie);
}

@media only screen and (min-width: $md_break) {
    .relatedContentBox {
        @include box-sizing();
        @include rem('padding', 13px 24px 6px);
        @include rem('border', 1px solid $zombie);
        @include border-radius();
    }
}


// Related box styling
// ------------------------------------
.relatedContent,
.inlinerelated,
.factbox {
    @include rem('margin', 0 0 28px);
    
    h1, h2, h3, h4, h5, h6 {
        margin-top: 0;
    }

    &.noFloat {
        clear: both;
        float: none;
        display: inline-block;
        width: 100%;
        margin-left: 0;
        @include rem('margin-bottom', 35px);
    }

    @media only screen and (min-width: $md_break) {
        width: 48%;
        float: right;
        clear: right;
        @include rem('margin-left', 38px);
    }
}

// List styling
.relatedContent,
.inlinerelated {

    ul,
    ol {
        list-style: none;
        padding: 0;
        @include rem('margin', -7px 0 21px);
        @include clearfix();

        li {
            list-style-image: none;
            @include rem('border-bottom', 1px solid $janna);

            &:last-child {
                border-bottom: 0 none;
            }
        }

        a {
            display: block;
            @include rem('padding', 10px 14px);
            background: url(../images/default_list_style_image.svg) no-repeat 1px 21px transparent;
        }
        
        &.with_date {
            
            a:hover,
            a:active {
                text-decoration: none;

                .title {
                    text-decoration: underline;
                }
            }
            
            .date small {
                color: $emperor;
                @include rem('font-size', 17px);
                line-height: ( 22px / 17px );
            }
        }
    }
    
    &.noFloat ul li:last-child {
        @include rem('border-bottom', 1px solid $janna);
    }
}


// Related images
// ------------------------------------
.related_image {
    
    // The image
    .image_link {
        display: inline-block;
        width: 100%;
        @include rem('margin-bottom', 4px);
        position: relative;
        
        img {
            width: 100%;
        }
        
        &:before {
            content: "";
            position: absolute;
            @include rem('right', 6px);
            @include rem('bottom', 6px);
            @include rem('width', 32px);
            @include rem('height', 28px);
            // .open_lightbox_icon
            background: rgba($black, 20/100) url($main_sprite_url) no-repeat -288px -194px;
            @include border-radius(2px);
            opacity: .7;
        }

        &:hover:before,
        &:active:before,
        &:focus:before {
            opacity: 1;
        }
    }
    
    // Stort-bilde-link med forstørelsesglass
    .imageset {
        display: none;
    }

    // Bildetekst
    .imgtext,
    .photographer {
        color: $emperor;
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
    }
}

#topPicture {
    @include rem('margin-bottom', 21px);
    clear: both;
    @include clearfix();
}

// Slideshow // simplepublishing
.related_image_container.slideshow {
    position: relative;
    @include clearfix();
    
    .imageset {
        display: inherit;
        float: right;
        @include rem('margin', -2px 0 6px 12px);
        
        a {
            position: relative;
            @include rem('padding-left', 24px);
            @include rem('font-size', 16px);
            line-height: ( 21px / 16px );
            
            &:before {
                content: "";
                position: absolute;
                @include rem('left', -7px);
                @include rem('top', -6px);
                @include rem('width', 32px);
                @include rem('height', 32px);
                background: transparent url($main_sprite_url) no-repeat -256px -192px; // .open_slideshow_icon
            }            
        }


        img {
            display: none;
        }
    }
}

// Slideshow // 3 small images
.related_image_gallery_container {
    
    .related_image_gallery {
        @include clearfix();
        @include rem('margin-bottom', 7px);
    }
    
    .related_image_gallery_item {
        float: left;
        display: inline-block;
        width: 31.1111111111%;
        margin-right: 3.3333333333%;
    
        &.last {
            margin-right: 0;
        }
    }
    
    .image_link {
        display: block;
        
        img {
            width: 100%;
            margin: 0 !important;
        }
    }
    
    // Bildeserie-tekst
    .imageset {
        a {
            display: inline-block;
            position: relative;
            @include rem('padding-left', 24px);
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
            
            &:before {
                content: "";
                position: absolute;
                @include rem('left', -7px);
                @include rem('top', -6px);
                @include rem('width', 32px);
                @include rem('height', 32px);
                background: transparent url($main_sprite_url) no-repeat -256px -192px; // .open_slideshow_icon
            }            
        }

        img {
            display: none;
        }
    }
}


// Multimedia - artikkel & kolonnemal
// ------------------------------------
.relatedContent ul .multimedia_link,
.ece_module_references ul .multimedia_link {    
    display: block;
    position: relative;
    @include rem('padding', 10px 16px 10px 30px);
    text-decoration: none;
    background-image: none;

    &:hover .file_title,
    &:active .file_title {
        text-decoration: underline;
    }

    .icon {
        position: absolute;
        text-indent: -3000em;
        overflow: hidden;
        @include rem('top', 12px);
        @include rem('left', 2px);
        @include rem('width', 18px);
        @include rem('height', 24px);
        background: url(../images/file.svg) no-repeat 0 0 transparent;

        &.icon_pdf {
            background-image: url(../images/pdf.svg);
        }
        &.icon_doc,
        &.icon_docx,
        &.icon_word {
            background-image: url(../images/doc.svg);
        }
        &.icon_xls,
        &.icon_xlsx,
        &.icon_excel {
            background-image: url(../images/xls.svg);
        }
        &.icon_ppt,
        &.icon_pps,
        &.icon_pptx,
        &.icon_ppsx,
        &.icon_powerpoint {
            background-image: url(../images/ppt.svg);
        }
        &.icon_htm,
        &.icon_html {
            background-image: url(../images/html.svg);
        }
    }

    .file_title {
        @include hyphens();
    }

    .file_size,
    .file_date {
        color: $emperor;
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
    }
}


// Bildeserie
// ------------------------------------
.inlinerelated {
    
    .lightbox img {
        @include rem('margin-right', 4px);
    }
    
    .last .lightbox img {
        margin-right: 0;
    }
}



// Rearrange the article elements on mobile in a different order
// A copy of this is also in print.css
// -----------------------------------------
@media only screen and (max-width: $md_break_max) {
    
    #newsDetail .module {
        @include flexbox();
        @include flex-direction($value: column);
        width: 100%;
        
        .teaser_kicker {
            @include order($int: -26);            
        }
        
        .article_header {
            @include order($int: -24);
        }
        
        .art_event_date {
            @include order($int: -22);
        }
        
        .art_event_link {
            @include order($int: -20);
        }
        
        .ingress {
            @include order($int: -18);
        }
        
        .articleinfo_top {
            @include order($int: -16);            
        }
        
        .first_image_container {
            @include order($int: -14);
        }
        
        .article_body_content {
            @include order($int: -10);
        }
    }
    .article--escenic {
        @include flexbox();
        @include flex-direction($value: column);
        width: 100%;

        .article__figure--top-figure {
            @include order($int: -4);
        }
        .article__header {
            @include order($int: -3);
        }
        .article__excerpt {
            @include order($int: -2);
        }
        .article__content {
            @include order($int: 0);
        }
        // Escenic-elementer som vises i nye sne-artikler skal vises under hovedinnholdet på mobil
        &-ece-content {
            @include order($int: 1);
        }
        // Første bilde, som kommer etter ingress, skal vises rett etter ingress
        .article__excerpt + .related_image_container {
            @include order($int: -1);
        }
        .article__footer-print-link-container {
            @include order($int: 2);
        }
        .article__byline {
            @include order($int: 3);
        }
    }
    // Remember print
}




// Print link
// ------------------------------------
.article_footer_print_link_container {
    clear: both;
    @include clearfix();
    @include rem('border-bottom', 1px solid $zombie);
    @include rem('padding-bottom', 7px);
    @include rem('margin', 0 0 6px);
    
    a {
        position: relative;
        display: inline-block;
        @include rem('padding', 0 7px 0 27px);
    }
    
    .print_icon {
        position: absolute;
        @include rem('top', -1px);
        @include rem('left', -8px);
    }
    
    .print_link_txt {
        @include rem('font-size', 17px);
        line-height: ( 28px / 17px );
    }
}



// Byline
// ------------------------------------
.articleinfo {
    @include clearfix();
    @include rem('font-size', 17px);
    line-height: ( 28px / 17px );
    
    .author a {
        text-decoration: underline;
    }
    
    .separator {
        @include rem('margin', 0 2px);
    }
    
    // Byline top
    &.articleinfo_top {
        @include rem('padding', 14px 0 12px);
        @include rem('border-top', 1px solid $zombie);
        @include rem('border-bottom', 1px solid $zombie);
        @include rem('margin-bottom', 28px);
    }
    
    @media only screen and (min-width: 860px) {
        &.articleinfo_top {
            @include clearfix();

            .date {
                float: left;
            }
            .author {
                float: right;
                text-align: right;
            }
        }
    }
}


// En escenic artikkel alene på en forside viser aktuelle tema og etjenester
// -> og trenger avstand
// Eks. /politikk/budsjett/avgifter-og-gebyrer
.module + .row_no_clearfix.equal_height_container.mbxl {
    @include rem('margin-top', 56px);  // .mbj
}

// En escenic artikkel alene på en forside på politikker.jsp viser kontaktboks under
// -> og trenger avstand
// Eks. /politikk/byradet/harald-schjelderup-ap
.module + .contact_section {
    @include rem('margin-top', 56px);  // .mbj
}






// Pressemelding
// ------------------------------------
.press_release_info {
    
    .press_release_info_title {
        margin-top: 0;
        @include SourceSansProSemibold();
        @include rem('font-size', 22px);
        line-height: ( 28px / 22px );
    }
    .press_release_info_meta {
    }
}
.module.pressemelding .article_footer_print_link_container {
    border-bottom: 0 none;
    @include rem('margin-bottom', -35px);
}




