.form-element {
  width: 100%;

  &__label {
    width: 100%;
    margin-bottom: 0;
    @include SourceSansProSemibold;

    &--checkbox{
      @include rem('font-size', 17px);
      @media only screen and (min-width: $md_break){
        @include rem('font-size', 19px);
        @include SourceSansProRegular;
      }
    }
    &.screen-reader-text {
        width: 1px; // reset the default to // screen-reader-text
    }
  }

  &__helptext {
    margin: 0;
    @include rem('font-size', 17px);
    color: $emperor;
    @include SourceSansProRegular;

    &--error{
      display: none;
      color: $brand_danger;
      @include rem('font-size', 17px); //refactor
      @include rem('margin-top', 6px);
    }
  }

  &__textfield {
    color: $link_color;
    width: 100%;
    @include rem('border', 1px solid $form_main_color);
    @include SourceSansProRegularItalic();
    @include border-radius();
    margin: 0;
    @include rem('text-indent', 4px);
    @include rem-box-shadow(0 1px 2px $alto inset);
  }

  &__textarea{
    color: $link_color;
    width: 100%;
    @include rem('border', 1px solid $form_main_color);
    @include SourceSansProRegularItalic();
    @include border-radius();
    margin: 0;
    @include rem('text-indent', 4px);
    @include rem-box-shadow(0 1px 2px $alto inset);
  }

  &__number{
    color: $link_color;
    width: 100%;
    @include rem('border', 1px solid $form_main_color);
    @include SourceSansProRegularItalic();
    @include border-radius();
    margin: 0;
    @include rem('text-indent', 4px);
    @include rem-box-shadow(0 1px 2px $alto inset);

    &--without-spinner{
      -moz-appearance:textfield !important;

      &::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
        -webkit-appearance: none !important;
        margin: 0 !important;
        -moz-appearance:textfield !important;
      }

      &::-ms-clear {
        display: none;
      }

    }
  }

  &__checkbox{
    @include rem-box-shadow(0 1px 2px $alto inset);
    &:focus + label {
      @include outline();
    }
  }

  &__checkbox {
    opacity: 0;
    position: absolute;
    @include box-shadow(none);
  }

  &__checkbox + label span {
    cursor: pointer;
    display: inline-block;
    @include rem('height', 20px);
    @include rem('width', 20px);
    vertical-align: middle;
    @include border-radius();
    @include rem('border', 1px solid $form_main_color);
    @include rem('margin', -2px 4px 0 0);
  }

  &__checkbox + label {
    cursor: pointer;
  }

  &__checkbox:checked + label span {
    background-color: $link_color;
    background-repeat: no-repeat;
    background-size: 19px 19px;
    @include pseudo-icon('.svg-icon-checkmark', $margin: 0 0 2px 1px);
  }

  &__dropdown {
    background-color: $white;
    background-image: -webkit-linear-gradient(hsla(0, 0%, 100%, 0), hsla(0, 0%, 100%, 0)); // Removing the inner shadow, rounded corners on iOS inputs
    @include border-radius();
    @include box-sizing();
    color: $black;
    width: 100%;
    max-width: 100%;
    @include rem('text-indent', 4px);
      
    cursor: pointer;
    display: inline-block;
    @include rem('font-size', 20px);
    line-height: (28px / 20px);
    @include rem('margin', 0 10px 0 0);
    -webkit-appearance: none;
    -moz-appearance: none;
    min-width: 100px;
    @include rem('border', 1px solid $form_main_color);
    @include rem('padding', 7px 40px 7px 10px);
    @include rem-box-shadow(0 1px 2px $alto inset);

    &::-ms-expand {
      display: none;
    }
  }

  &__dropdown select::-ms-expand {
    display: none;
  }

  &__label--dropdown-icon {
    position: relative;
    width: auto;
    @include pseudo-icon($extend: ".svg-icon-arrow-down",
    $position: 'after',
    $margin: 47px 2px 0 0,
    $opacity: 1);

    &:after {
      color: $white;
      pointer-events: none;
      position: absolute;
      @include rem('right', 9px);
      @include rem('top', -2px);
    }

    &--without-label {
      &:after {
        @include rem('right', 9px);
        @include rem('top', -28px);
      }
    }
  }

  &__radio {
    opacity: 0;
    position: absolute;
    @include box-shadow(none);

    &:focus + label {
      @include rem('outline', 3px solid $link_outline_color);
    }
  }
    
  &__radio + label {
    position: relative;
    @include rem('padding', 1px 0 0 30px);
    cursor: pointer;
    @include SourceSansProRegular;
  }

  &__radio + label::before {
    left: 0;
    @include rem('top', 1px);
    @include rem('width', 20px);
    @include rem('height', 20px);
    @include rem-box-shadow(0 1px 2px $alto inset);
    border-radius: 50%;
    @include rem('border', 1px solid $form_main_color);
  }

  &__radio + label::before,
  &__radio:checked + label::after {
    position: absolute;
    content: '';
    display: inline-block;
  }

  &__radio + label::after {
    @include rem('left', 6px);
    @include rem('top', 7px);
    @include rem('width', 10px);
    @include rem('height', 10px);
    background: $form_main_color;
    @include border-radius(50%);
  }

  // Disabled
  &__checkbox[disabled] {
      opacity: 0;
      cursor: not-allowed;
      
      & + label span {
          cursor: not-allowed;
          border-color: $grey7;
      }
      
      & + label {
          cursor: not-allowed;
          color: $grey7;
      }
  }
    
  &__radio[disabled] {
      opacity: 0;
      cursor: not-allowed;
      
      & + label::before {
          cursor: not-allowed;
          border-color: $grey7;
      }
      
      & + label {
          cursor: not-allowed;
          color: $grey7;
      }
  }

  &--error {
    @include rem('border', 1px solid $brand_danger);
  }

  &--indented {
      @include rem('margin-left', 30px);
  }
}
