@charset "UTF-8";

// Setup for font: Source Sans Pro
// --------------------------------------------------

// mixin for bullet proof font declaration syntax
@mixin declare-font-face($font-localname, $font-filename, $font-weight : normal, $font-style :normal, $font-stretch : normal) {
  
  $font-family: 'Source Sans Pro';
  $font-path: '../font/sourcesanspro' !default;
  
  @font-face {
    font-family: '#{$font-family}';
    font-weight: $font-weight;
    font-style: $font-style;
    font-stretch: $font-stretch;
    src: local('#{$font-filename}'),
         local('#{$font-localname}'),
         url($font-path + '/EOT/#{$font-filename}.eot') format('embedded-opentype'),
         url($font-path + '/WOFF/OTF/#{$font-filename}.otf.woff') format('woff'),
         url($font-path + '/OTF/#{$font-filename}.otf') format('opentype'),
         url($font-path + '/TTF/#{$font-filename}.ttf') format('truetype');
  }
}

@include declare-font-face('Source Sans Pro', 'SourceSansPro-Regular', 400);
@include declare-font-face('Source Sans Pro', 'SourceSansPro-It', 400, italic);
@include declare-font-face('Source Sans Pro Semibold', 'SourceSansPro-Semibold', 600);
@include declare-font-face('Source Sans Pro Bold', 'SourceSansPro-Bold', 700);
