.servicecat_button {
    margin-top: 0;
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    @include SourceSansProRegular();
    
    a {
        background: url(../images/list_arrows_dobbel.svg) no-repeat 7px 0 $rum_swizzle;
        display: block;
        @include rem('border', 1px solid $almond);
        @include border-radius();
        @include rem('padding', 6px 12px 6px 26px);
        @include box-sizing();
        width: 100%;
    }
}

.button--search{
    margin: 0;
    @include rem('border', 1px solid $link_color);
    @include rem('padding', 5px 23px 5px 15px);
    background-color: $danube;
    @include rem-box-shadow(0 1px 0 rgba($white, 32/100) inset, 0 -1px 0 rgba($link_color, 32/100) inset);
    color: $white;
    @include rem('font-size', 24px);
    display: flex;
    justify-content: space-between;

    @media only screen and (min-width: $md_break){
        @include rem('padding', 5px 13px 5px 13px);
    }

    .button__text--hidden{
        clip: auto;
        height: auto;
        width: auto;
        overflow: hidden;
        position: relative;

        @media only screen and (min-width: $md_break) {
            clip: rect(1px, 1px, 1px, 1px);
            height: 1px;
            width: 1px;
            overflow: hidden;
            position: absolute;

        }
    }

}

.button{
    &--success{
        color: $brand_success;
        @include rem('border', 1px solid $brand_success);

        &:hover{
            color: $white;
            background-color: $brand_success;
            @include rem('border', 1px solid $brand_success);
        }

        &.active{
            @include rem('border', 1px solid $brand_success);
            background-color: $brand_success;
            color: $white;

            &:hover{
                @include rem('border', 1px solid $brand_success);
                background-color: darken($brand_success, 5);
            }
        }
    }

    &--danger{
        color: $main_red;
        @include rem('border', 1px solid $main_red);

        &:hover{
            color: $white;
            background-color: $main_red;
            @include rem('border', 1px solid $main_red);
        }

        &.active{
            @include rem('border', 1px solid $main_red);
            background-color: $main_red;
            color: $white;

            &:hover{
                @include rem('border', 1px solid $main_red);
                background-color: darken($main_red, 5);
            }
        }
    }
}


