// Soekeresultat
// ---------------------------------------------------


// Collapsed on mobile
// -------------------------------------------

@media (max-width: $md_break_max) {
	.collapsed_on_mobile.collapsed ul {
		height: 0;
		overflow: hidden;
		border: none;
	}
}


// Fasetter
// ---------------------------------------

.fasett_header_mobile {
	@include rem('font-size', 20px);
	line-height: ( 28px / 20px );
	@include rem('margin', 0 0 4px);
    @include rem('border', 1px solid $alto);
    @include border-radius();
	
	a {
		display: block;
		@include rem('padding', 6px 12px 6px 28px);
		background: url(../images/blue_list_style_image.svg) no-repeat 17px 17px transparent;
	}
    
    .search_hits {
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
        color: $emperor;
    }

    // If there is just one fasett
    &.single {    	
        border-color: transparent;
    }
	
	@media (min-width: $md_break) {
		display: none;
	}
}

.fasett_header_desktop {
    @include SourceSansProRegular();
    
    @media (max-width: $md_break_max) {
    	display: none;
	}
}


.link_list--search-facetts {
            
    .search_hits {
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
        color: $emperor;
    }
	
	li {
		
        a:hover,
        a:active {
            text-decoration: none;
            .fasett_name {
                text-decoration: underline;
            }
		}
		
		&.selected {
            @include SourceSansProSemibold();
		}
		
		// If there is just one fasett
		&.single {
            /*
			@include rem('font-size', 20px);
			line-height: ( 28px / 20px );
			@include rem('padding', 13px 0 9px 33px);
			@include box-sizing();
    		background: url(../images/default_list_style_image.svg) no-repeat 18px 21px transparent
            */
            
            @include SourceSansProRegular();
            @include rem('padding', 10px 12px);
            background: url(../images/default_list_style_image.svg) no-repeat transparent;
            @include rem('background-position', 2px 20px);
		}
	}
	
	@media (min-width: $md_break) {
		@include multicolumn($count: 2, $gap: 20px);

		&:before,
        &:after {
			content: none;
		}


		& li {
			box-sizing: content-box;
			display: inline-block;
			width: 100%;
		}
        
		& a {
            @include rem('padding', 7px 10px);
            @include rem('background-position', 0 17px);
			@include rem('border', 3px solid $white);
		}

		& a:focus,
        & a:active {
            border-color: $link_outline_color;
			outline: 0;
		}

		/*
        position: relative;

        &:after {
            position: absolute;
            left: 0;
            bottom: 0;
            content: "";
            width: 100%;
            @include rem('border-bottom', 1px solid $janna);
        }
        */
	}
}

// If there is just one fasett
.fasett_header_mobile.single {    	
    border-color: transparent;
}



// Search results
// ---------------------------------------

ol.search_results {
	list-style: none;
	padding-left: 0;
	
	li {
		@include rem('padding', 13px 0 14px);
		@include rem('border-bottom', 1px solid $janna);
	}
    
    .search_result_link {
        text-decoration: none;
        display: inline-block;
        
        &:hover .search_result_title {
            text-decoration: underline;
        }
    }
	
	.search_result_title {
        margin: 0;
		@include rem('font-size', 26px);
		line-height: ( 28px / 26px );
        @include SourceSansProRegular();
	}
	
	.search_result_breadcrumbs {
        display: block;
        color: $emperor;
		@include rem('font-size', 16px);
		line-height: ( 21px / 16px );
		@include rem('margin-bottom', 7px);
		
		span {
			display: none;
		}
		
		span.section:last-child,
		span.section:nth-last-child(2) {
			display: inline;
		}
        
		span.section:last-child span.separator {
            display: inline;
		}
	}
	
	.search_result_date {
		color: $emperor;
	}
	
	.search_result_excerpt {
        @include rem('max-width', 600px);
	}
}



// No results
// ---------------------------------------
.no_results {
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
	@include rem('margin-bottom', 35px);
}
