// Links
// -------------------------------------
.link {
    
    &--two-arrows {
        display: inline-block;
        @include rem('padding-left', 26px);
        background: url(../images/list_arrows_dobbel.svg) no-repeat 7px -6px;
    }

    &--underline {
        text-decoration: underline;
    }
}


// Read more link
// -------------------------------------
.read_more_link { // for default tekst - font-size: 19px
    text-decoration: none;
    @include rem('padding-right', 10px);
    background-color: transparent;
    background-repeat: no-repeat;
    background-image: url("../images/blue_list_style_image.svg");
    background-position: right 10px;
    
    &--16 {
        @include rem('padding-right', 9px);
        background-position: right 8px;
    }
    
    &--17 {
        background-position: right 9px;
    }
    
    &--18 {
        background-position: right 9px;
    }
    
    &--20 {
        background-position: right 11px;
    }
    
    &--21 {
        @include rem('padding-right', 12px);
        background-position: right 11px;
    }
    
    &--22 {
        @include rem('padding-right', 13px);
        background-image: url("../images/blue_list_style_image_large.svg");
        background-position: right 10px;
    }
    
    &--24 {
        @include rem('padding-right', 13px);
        background-image: url("../images/blue_list_style_image_large.svg");
        background-position: right 12px;
    }
    
    &--26 {
        @include rem('padding-right', 13px);
        background-image: url("../images/blue_list_style_image_large.svg");
        background-position: right 14px;
    }
    
    &--28 {
        @include rem('padding-right', 13px);
        background-image: url("../images/blue_list_style_image_large.svg");
        background-position: right 15px;
    }
    
    &--30 {
        @include rem('padding-right', 14px);
        background-image: url("../images/blue_list_style_image_large.svg");
        background-position: right 17px;
    }
    
    &--32 {
        @include rem('padding-right', 15px);
        background-image: url("../images/blue_list_style_image_large.svg");
        background-position: right 18px;
    }
}


// External links = defined as urls other than domain: bergen.kommune.no
// -------------------------------------
.external_link {
    
    &:before {
        white-space: pre-wrap;
        display: inline-block;
    }
    
    &:after {
        content: '';
        @include rem('width', 12px);
        @include rem('height', 12px);
        background-image: url($main_sprite_url);
        @include rem('background-position', -266px -234px); // external_icon
        background-color: transparent;
        background-repeat: no-repeat;
        display: inline-block;
        text-decoration: none;
        overflow: hidden;
        white-space: pre-wrap;
        @include rem('margin', 0 2px -1px 3px);
    }
    
    // Reset
    &.read_more_link,
    & > .read_more_link {
        padding-right: 0;
        background-image: none;
    }
}
// Reset
.ece_hvaskjer_grid .ece_module_content_container .ece_module_title .external_link .read_more_link {
    padding-right: 0;
    background-image: none;
}
// Searchresult
.search_result_link.external_link {
    position: relative;
    
    &:after {
        position: absolute;
        @include rem('top', 12px);
        @include rem('right', -17px);
    }
}
// Button
.button.external_link:after {
    @include rem('margin-left', 8px);
}
// Button Hover
.button.external_link:hover:after {
    @include rem('background-position', -299px -235px); // external_icon {hover}
}
// Button - blue - active
.button.active.external_link:after {    
    @include rem('background-position', -299px -235px); // external_icon {hover}
}



// Skjema lenke
// -------------------------------------
.application-link {
    position: relative;
    display: inline-block;
    @include rem('padding-left', 40px);

    &:before {
        position: absolute;
        @include rem('top', -1px);
        @include rem('left', 0);
        content: "";
        @include rem('width', 32px);
        @include rem('height', 32px);
        background: url($main_sprite_url) no-repeat -160px -96px transparent; // .application_icon
    }

    &--small {
        display: block;
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
        @include rem('padding', 14px 12px 13px 45px);
        margin: 0;

        &:before {
            @include rem('top', 8px);
            @include rem('left', 11px);
        }
    }

    &--large {
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        text-decoration: underline;
        @include rem('padding', 3px 7px 4px 40px);
        @include rem('margin-bottom', 14px);

        &:before {
            @include rem('top', 1px);
            @include rem('left', 3px);
        }
    }

    &--important {
        display: block;
        color: $white;
        @include rem('padding', 10px 40px 11px);
        @include rem('font-size', 26px);
        line-height: ( 28px / 26px );
        text-decoration: underline;
        text-shadow: 0 1px 2px $link_color;
        background: url(../images/blue_arrow_bg.svg) no-repeat center right $danube;
        background-size: cover;
        @include rem('margin-bottom', 14px);

        &:before {
            @include rem('top', 8px);
            @include rem('left', 3px);
            background-position: -160px -127px;// .application_icon.white
        }
    
        &:hover,
        &:active {
            color: $white;
        }
    }
}


// Opprinnelig "Les mer-container"
// -------------------------------------
.read_more_link_container {
    text-align: right;
    @include rem('font-size', 17px);
    line-height: ( 21px / 17px );
    
    &.toarchive {
        @include clearfix();
        float: left;
        width: 100%;
        @include rem('margin-top', -14px);
        
        a {
            text-decoration: none;
            
            &:hover,
            &:active {
                text-decoration: underline;
            }
        }
    }

    &--top-border {
        @include rem('border-top', 1px solid $janna);
        @include rem('padding-top', 6px);
    }

    &--larger-font {
        @include rem('font-size', 19px);
        line-height: ( 28px / 19px );
        
        .read_more_link--17 {
            background-position: right 10px;
        }
    }
}


// Last inn flere - Denne kan utvides
// ----------------------------------------
.load-more-container {
    @include clearfix();
    @include rem('margin-bottom', 56px);
	
    &--20 {
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
    }
	
    &--border-bottom {
        @include rem('padding-bottom', 6px);
        @include rem('border-bottom', 1px solid $alto);
    }
    
    &--centered {
        text-align: center;
        
        a {
            text-decoration: underline;
        }
    }
}





