// TOUTS
// ---------------------------------------------------

// Aktuelle tema, etjenester,++
// -------------------------------------------
.tout_container {
    width: 100%;
    @include rem('padding', 21px 21px 7px);
    @include rem('border', 1px solid $zombie);
    @include border-radius();
    @include box-sizing();
    @include rem('margin-bottom', 28px);
    @include hyphens();
    
    .tout_header {
        @include SourceSansProRegular();
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        margin-top: 0;
        @include rem('margin-bottom', 14px);
    }
    
    .tout_img_link {
        display: block;
        width: 100%;
    }
    
    .tout_img {
        display: block;
        width: 100%;
        @include rem('margin-bottom', 14px);
    }
    
    .tout_excerpt {
        @include rem('margin-bottom', 14px);
        
        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }
        
        p,
        ol,
        ul {
            @include rem('margin-bottom', 14px);
        }
        
        a {
            text-decoration: underline;
        }
        
        ul {
            padding-left: 0;

            li {
                list-style-image: none;
                @include rem('padding', 10px 0 10px 14px);
                @include rem('border-bottom', 1px solid $janna);
                background: url(../images/default_list_style_image.svg) no-repeat transparent;
                @include rem('background-position', 1px 20px);
                
                &:last-child {
                    border-bottom: 0 none;
                }
            }

            a {
                text-decoration: none;

                &:hover,
                &:active,
                &:focus {
                    text-decoration: underline;
                }
            }
        }
    }    
}

// Spesial tout - Skoleruten, arbeidsplaner, Bydelsavdelinger
// ----------------------------------------------------------------
.tout_container--special {
    
    .tout_header {
        @include rem('border-bottom', 2px solid $zombie);
        @include rem('margin-bottom', 12px);
        
        a {
            color: $black;
        }
    }
    
    .tout_excerpt ul {
        @include rem('margin', 4px 0 0 14px);
        
        li {
            background-position: 1px 12px;
            @include rem('padding', 2px 0 2px 14px);
            border-bottom: 0 none;
        }
    }
    
    .tout_read_more_link_container {
        
        a {
            text-decoration: none;

            &:hover,
            &:active {
                text-decoration: underline;
            }
        }
    
        &--border-top {
            @include rem('border-top', 1px solid $zombie);
            @include rem('margin-top', 12px);
            @include rem('padding-top', 10px);
        }
    }
}


// Luftkvalitet etjeneste
// -----------------------------------------------------------------------------
.tout_container .tout_excerpt .air_quality_list li {
    background-image: none;
    padding-left: 0;
    
    .air_quality_colorbox {
        display: inline-block;
        @include rem('width', 16px);
        @include rem('height', 16px);
        @include rem('margin-right', 4px);
        position: relative;
        @include rem('top', 2px);
        @include border-radius(50%);
    }

    a:focus,
    a:hover,
    a:active {
        text-decoration: none;

        .air_quality_station_name {
            text-decoration: underline;
        }
    }
}
.tout_container .tout_excerpt .air_quality_list--frontpage {
    @include rem('border-bottom', 1px solid $janna);
    margin-bottom: 0;
}



// Service link F.eks: Feilmelding
// ---------------------------------------------------
.service_shortcut_container {
    position: relative;
    text-align: center;

    .service_shortcut_title {
        @include rem('border-top', 1px solid $almond);
        @include SourceSansProRegular();
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include rem('padding-top', 55px);
        @include rem('margin', -35px 0 21px);
        
        a {
            color: $text_color;
        }
    }
    .service_icon {
        position: relative;
        z-index: 2;
        float: none;
        margin: 0 auto;
        @include rem('border', 1px solid $almond);
        @include border-radius($border_radius_larger);
    }
}


// Service link in a tout style
// ---------------------------------------------------
.tout_container.service_shortcut_container {
    @include rem('padding', 24px 24px 32px);
    
    .service_shortcut_title {
        @include rem('margin-bottom', 18px);
    }
}


// External URL
// ---------------------------
.external_url_header {
    @include SourceSansProRegular();
    margin-top: 0;
    @include clearfix();
    
    .external_url_link {
        display: block;
        color: $text_color;
        @include rem('font-size', 22px);
        line-height: ( 28px / 22px );
        text-align: center;
        @include rem('padding', 47px 12px 21px);
        position: relative;
        @include rem('margin-top', 35px);
        @include rem('border', 1px solid $zombie);
        @include border-radius();

        &:hover,
        &:active {
            color: $text_color;
            text-decoration: none;
        }

        // Link text
        .external_url_link_text {
            display: block;
            color: $link_color;
            @include rem('font-size', 28px);
            line-height: ( 35px / 28px );
            @include rem('padding', 9px 4px 5px);
            margin: 0;
            
            .external_link:after {
                @include rem('margin-left', 6px);
            }
        }
        
        &:hover .external_url_link_text,
        &:active .external_url_link_text {
            color: $link_color_hover;
            text-decoration: underline;
        }

        // Container icon
        &:before {
            content: "";
            position: absolute;
            @include rem('top', -32px);
            right: 0;
            left: 0;
            margin: 0 auto;
            @include rem('border', 1px solid $zombie);
            background-color: $white;
            @include border-radius(50%);
            @include rem('width', 64px);
            @include rem('height', 64px);
            background: url($service_sprite_url) no-repeat $white -192px -128px;
        }
    }
}


// Internal URL / Underavdelinger - lenke
// -------------------------------------------
.box_link_with_icon_container {
    @include rem('padding-top', 33px);
    
    .box_link_with_icon {
        display: block;
        width: 100%;    
        @include box-sizing();
        @include rem('font-size', 28px);
        line-height: ( 35px / 28px );
        text-align: center;
        @include rem('padding', 48px 12px 22px);
        position: relative;
        @include rem('border', 1px solid $zombie);
        @include border-radius();
        
        // Icon container
        &:before {
            content: "";
            position: absolute;
            @include rem('top', -32px);
            right: 0;
            left: 0;
            margin: 0 auto;
            @include rem('border', 1px solid $zombie);
            background-color: $white;
            @include border-radius(50%);
            @include rem('width', 64px);
            @include rem('height', 64px);
        }

        &.department_link_icon:before {
            background: url($service_sprite_url) no-repeat $white -256px -192px;
        }
    }
}