// En oppslått artikler
// ----------------------------------------------------------
.article {
    
    // Overskrift
    &__header {
        color: $black;
        @include rem('font-size', 26px);
        line-height: ( 28px / 26px );
        @include rem('min-height', 28px);
        @include hyphens();
        @include rem('border-bottom', 2px solid $zombie);
        @include rem('padding-bottom', 2px);
        @include rem('margin', 0 0 19px);

        @media only screen and (min-width: $md_break) {
            @include rem('font-size', 30px);
            line-height: ( 35px / 30px );
            @include rem('margin-top', -5px);
        }
    }

    // Ingress
    &__excerpt {
        @include SourceSansProSemibold();
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('max-width', 600px);
        @include rem('margin-bottom', 21px);
    }

    // Hovedtekst
    &__content {
        @include clearfix();
        @include rem('margin-bottom', 28px);

        img {
            max-width: 100%;
        }

        // Hvis overskriften er første elementet i hovedtekstfeltet
        // Og også hvis cke viser en blå linje på hover foran en første overskrift
        h2:first-child,
        h3:first-child,
        h4:first-child,
        h5:first-child,
        span:first-child + h2,
        span:first-child + h3,
        span:first-child + h4,
        span:first-child + h5 {
            margin-top: 0;
        }

        // Hvis overskriften er første elementet etter et eller flere høyrejusterte bilder i hovedtekstfeltet
        figure.align_right:first-child + h2,
        figure.align_right:first-child + h3,
        figure.align_right:first-child + h4,
        figure.align_right:first-child + h5,
        figure.align_right:first-child + figure.align_right + h2,
        figure.align_right:first-child + figure.align_right + h3,
        figure.align_right:first-child + figure.align_right + h4,
        figure.align_right:first-child + figure.align_right + h5,
        figure.align_right:first-child + figure.align_right + figure.align_right + h2,
        figure.align_right:first-child + figure.align_right + figure.align_right + h3,
        figure.align_right:first-child + figure.align_right + figure.align_right + h4,
        figure.align_right:first-child + figure.align_right + figure.align_right + h5 {
            margin-top: 0;
        }

        // Hvis en liste eller filvedlegg vises direkte etter en overskrift
        h2 + ul,
        h2 + ol,
        h3 + ul,
        h3 + ol,
        h4 + ul,
        h4 + ol,
        h5 + ul,
        h5 + ol {
            @include rem('margin-top', 7px);
        }

        // Lister
        ul li,
        ol li {
            @include rem('margin-bottom', 14px);
            
            a {
                text-decoration: underline;
            }
        }

        // Avstand overskrift til filvedlegg
        h1 + .file-container,
        h2 + .file-container,
        h3 + .file-container,
        h4 + .file-container,
        h5 + .file-container {
            @include rem('margin-top', 14px);
        }

        // Fil-avstand // 7px er default avstand mellom to fil-containeregg
        .file-container {
            @include rem('margin-bottom', 28px);
        }
        .file-container + .file-container {
            @include rem('margin-top', -21px);
        }

        // Filer skal flyte bedre i hovedtekstfeltet, også rundt bilder
        .file-container {
            @include rem('padding-left', 30px);

            .file-link {
                display: inline;
                padding-left: 0;
                margin-left: 0;

                .file-icon {
                    @include rem('top', 5px);
                    @include rem('left', -30px);
                }
            }
        }

        // Maksbredde
        p, ul, ol, h1, h2, h3, h4, h5, h6 {
            @include rem('max-width', 600px);
        }
    }

    // Image figure lightbox link
    &__figure {
        &-link {
            display: inline-block;
            width: 100%;
            position: relative;

            &:before {
                content: "";
                position: absolute;
                @include rem('right', 6px);
                @include rem('bottom', 6px);
                @include rem('width', 32px);
                @include rem('height', 28px);
                // .open_lightbox_icon
                background: rgba($black, 20/100) url($main_sprite_url) no-repeat -288px -194px;
                @include border-radius(2px);
                opacity: .7;
            }

            &:hover:before,
            &:active:before,
            &:focus:before {
                opacity: 1;
            }
        }
    }

    // Footer print link
    &__footer-print-link-container {
        clear: both;
        @include clearfix();
        @include rem('border-bottom', 1px solid $zombie);
        @include rem('padding-bottom', 7px);
        @include rem('margin', 0 0 6px);

        a {
            position: relative;
            display: inline-block;
            @include rem('padding', 0 7px 0 27px);
        }

        .print_icon {
            position: absolute;
            @include rem('top', -1px);
            @include rem('left', -8px);
        }

        .print_link_txt {
            @include rem('font-size', 17px);
            line-height: ( 28px / 17px );
        }
    }

    // Byline
    &__byline {
        @include clearfix();
        @include rem('font-size', 17px);
        line-height: ( 28px / 17px );

        &-author a {
            text-decoration: underline;
        }

        &-separator {
            @include rem('margin', 0 7px);
        }
        
        &--top-border {
            @include rem('border-top', 1px solid $zombie);
            @include rem('padding-top', 14px);
        }

        @media only screen and (min-width: 860px) {
            &--top {
                @include clearfix();

                &-date {
                    float: left;
                }
                &-author {
                    float: right;
                    text-align: right;
                }
            }
        }
    }
}



