#skiplink_container,
header nav,
.nav_toggle_btn,
.login_link,
.main_search_container,
.print_link_container,
.module_header_with_print .print_link_container,
.article_footer_print_link_container,
.bc_container,
.service_image_desktop,
.feedback,
.org_menu_container,
.rssicon_floatRight,
.rssicon,
footer {
    display: none;
}
.page_header_with_print .print_link_container {
    display: none !important;
}

// General
// ---------------------------
html {
    font-size: 92%;
}

// Changing logo
// ---------------------------
.logo {
    display: none;
}
.share_and_print_logo {
    display: block;
    @include rem('width', 130px);
    @include rem('margin', -12px auto 0);
}

// Reset page header link styling
// ---------------------------
.h2_page_header_with_link {
    border-bottom: 0 none;
    margin-bottom: 0;
    
    h2 {
        min-height: inherit;
        @include rem('font-size', 14px);
        line-height: ( 14px / 14px );
        
        .page_header_link {
            color: $black;
            padding-left: 0;
            text-transform: uppercase;

            &:before {
                display: none;
            }
        }
    }
}

// Link list
// ---------------------------
.link_list li {
    border-bottom: 0 none;
    a {
        @include rem('padding', 7px 11px);
        background-position: 3px 16px;
    }
}


// Tjenestebeskrivelse
// ---------------------------
.service_image_mobile {
    float: right;
    max-width: 30%;
    @include rem('margin', 4px 0 28px 30px);
}

// Fremheved skjema - blå lenke
// ---------------------------
.application-link--important {
    @include rem('font-size', 24px);
    line-height: ( 28px / 24px );
    color: $link_color;
    background: 0 none;
    text-shadow: inherit;
    
    &:before {
        background-position: -160px -96px;
    }
    
    &:hover,
    &:active {
        color: $link_color_hover;
    }
}

// Skjema i accordion
.accordion_item.important .application-link-list li {
    border-bottom: 0 !important;
}

.accordion_content {
    display: inherit;
}

.map_link {
    display: none;
}

.contact_button {
    background-color: transparent;
    border: 0 none;
    padding: 0;
    text-decoration: underline;
    
    .icon {
        display: none;
    }
    
    .contact_button_info {
        display: inline;
        float: none;
    }
}


// Organisasjonsenhet
// ---------------------------
.org_main_col {
    margin: 0;
}


// Skoleruten
// ---------------------------
#vis_passerte,
#skjul_passerte,
.add_holidays_link {
    display: none;
}
.holidays_section {
    @include rem('margin-bottom', 32px);
}
h3.holidays_section_header {
    @include rem('font-size', 18px);
    line-height: ( 21px / 18px );
    @include rem('padding-bottom', 6px);
    @include rem('border-bottom', 1px solid $zombie);
    @include rem('margin-bottom', 4px);
}
.holidays_container {
    border: 0 none;
    @include rem('padding-bottom', 6px);
    @include rem('border-bottom', 1px solid $zombie);
    @include rem('margin-bottom', 6px);
    
    .holidays_header {
        padding: 0;
        background-color: transparent;
        border: 0 none;
        @include SourceSansProSemibold();
        @include rem('font-size', 14px);
        line-height: ( 21px / 14px );
    }
    .holidays_list {
        @include rem('font-size', 14px);
        line-height: ( 21px / 14px );
        
        .holidays_list_item {
            border: 0 none;
            
            .holiday_date {
                display: inline-block;
                padding: 0;
                border: 0 none;
                width: 20%;
            }
            
            .holiday_title_container {
                display: inline-block;
                padding: 0;
                width: 79%;
                
                .holiday_title {
                    display: inline-block;
                }

                .holiday_additional_info_list {
                    float: right;
                    display: inline-block;
                    padding-left: 0;
                    margin: 0;
                    @include rem('font-size', 14px);
                    line-height: ( 21px / 14px );
                    
                    li {
                        list-style-image: none;
                    }
                }
            }
        }
    }
}

// Politiker og utvalg - møtekalender
// ---------------------------
.holidays_container {
    .holidays_list {
        .meeting_title_link {
            text-decoration: none;
            color: $grey3;
            float: left;
        }

        .holiday_date {
            &__date {
                float: left;
                display: inline;
                padding-right: 10px;
            }

            &__time {
                float: right;
                display: inline;
                padding-right: 20px;
            }
        }
    }
}

.read_more_link_container {
    &--top-border {
        display: none;
    }
}



// Escenic
// ---------------------------

#newsDetail .module {
    @include flexbox();
    @include flex-direction($value: column);
    width: 100%;
        
    .teaser_kicker {
        @include order($int: -26);            
    }

    .article_header {
        @include order($int: -24);
    }

    .art_event_date {
        @include order($int: -22);
    }

    .art_event_link {
        @include order($int: -20);
    }

    .ingress {
        @include order($int: -18);
    }

    .articleinfo_top {
        @include order($int: -16);            
    }

    .first_image_container {
        @include order($int: -14);
    }

    .article_body_content {
        @include order($int: -10);
    }
}
.article--escenic {
    @include flexbox();
    @include flex-direction($value: column);
    width: 100%;

    .article__figure--top-figure {
        @include order($int: -4);
    }
    .article__header {
        @include order($int: -3);
    }
    .article__excerpt {
        @include order($int: -2);
    }
    .article__content {
        @include order($int: 0);
    }
    // Escenic-elementer som vises i nye sne-artikler skal vises under hovedinnholdet på mobil
    &-ece-content {
        @include order($int: 1);
    }
    // Første bilde, som kommer etter ingress, skal vises rett etter ingress
    .article__excerpt + .related_image_container {
        @include order($int: -1);
    }
    .article__footer-print-link-container {
        @include order($int: 2);
    }
    .article__byline {
        @include order($int: 3);
    }
}

.related_image_container {
    width: 50%;
    float: right;
    clear: right;
    @include rem('margin', 0 0 28px);
}



