// Escenic moduler
// ----------------------------------------------------------



// Module item
// -------------------------
.ece_module_container {
    width: 100%;
    @include clearfix();
    
    .ece_module_image_container {
        @include rem('margin-bottom', 14px);
        
        .ece_module_image_link {
            display: block;
            width: 100%;
        }
        .ece_module_image {
            display: block;
            background-color: $white_sand;
            width: 100%;
        }
    }
    .ece_module_content_container {
        @include rem('max-width', 600px);
        @include rem('margin-bottom', 7px);
        
        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }
        
        // Topp dato
        .ece_module_kicker_date {
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
            text-transform: uppercase;
        }
        
        // Stikktittel
        .ece_module_kicker {
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
            @include SourceSansProSemibold();
            text-transform: uppercase;
            // @include hyphens();
        }
        
        // Tittel
        .ece_module_title {
            @include SourceSansProRegular();
            @include rem('font-size', 24px);
            line-height: ( 28px / 24px );
            @include rem('margin-bottom', 1px);
            // @include hyphens();
        }
        
        // Ingress
        .ece_module_excerpt p {
            @include rem('margin-bottom', 14px);
        }
        
        // Meta: Les mer / Dato / Seksjonslenke
        .ece_module_meta {
            p {
                @include rem('margin-bottom', 14px);
            }
            .read_more_link {
                text-decoration: underline;
            }
            .date {
                @include rem('font-size', 17px);
                line-height: ( 21px / 17px );
                text-transform: uppercase;
            }
            .read_more_link + .date {
                @include rem('margin-left', 10px);
            }
            .read_more_link + .read_more_link {
                @include rem('margin-left', 10px);
            }
            .art_event_link {
                @include rem('margin-bottom', 14px);
            }
        }
        
        // Henvisninger
        .ece_module_references {
            @include rem('margin-bottom', 14px);

            ul {
                @include rem('padding-left', 14px);
                margin-bottom: 0;
                
                &.multimedia_references {
                    padding-left: 0;

                    li {
                        list-style-image: none;

                        .multimedia_link {
                            display: inline-block;
                            @include rem('padding-top', 7px);
                            @include rem('padding-bottom', 7px);

                            .icon {
                                @include rem('top', 8px);
                            }
                        }
                    }
                }
                
                &.link_references a,
                &.article_references a {
                    text-decoration: underline;
                }
            }
        }
    }
}


// Module list
// -------------------------
.ece_module_list {
    @include clearfix();
    
    .ece_module_container {
        @include rem('padding-bottom', 6px);
        @include rem('border-bottom', 1px solid $zombie);
        @include rem('margin-bottom', 28px);
    }
}


// Modules with 50% / 50% layout on tablet & desktop
// --------------------------------------------------
@media (min-width: $md_break) {
    .ece_module_list,
    .ece_hvaskjer_grid .toppsak,
    .teaser_small_container,
    .saker_container {
        
        .ece_module_container  {
            .ece_module_image_container {
                float: left;
                width: 48%;
                @include rem('margin', 0 21px 21px 0);
            }

            // Equal heights
            @include flexbox();

            .ece_module_image_container,
            .ece_module_content_container {
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex($fg: 1, $fs: 1, $fb: 50%);
            }
        }
        
    }
}

@media (min-width: $md_break) {
    // Hva skjer toppsak - 56%
    .ece_hvaskjer_grid .toppsak .ece_module_container .ece_module_image_container {
        @include flex($fg: 0, $fs: 0, $fb: 56%);
    }
}

@media (min-width: $lg_break) {
    // Hva skjer toppsak - 66%
    .ece_hvaskjer_grid .toppsak .ece_module_container .ece_module_image_container {
        @include flex($fg: 0, $fs: 0, $fb: 66%);
    }
}


// Hva skjer
// --------------------------------------------
.ece_hvaskjer_grid {
    
    // Avstand nedover
    .ece_module_container {
        @include rem('margin-bottom', 21px);
    }
    
    // Temaboks
    .grey_box {
        background-color: $white_sand;
        @include rem('padding-top', 35px);
        @include rem('min-height', 56px);
    }
    
    // Tablet & desktop
    @media (min-width: $md_break) {

        // Equal heights
        .row {
            @include flexbox();

            .col_md_4 {
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex($fg: 1, $fs: 1, $fb: 33.3333333%);
            }
            
            .col_md_6 {
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex($fg: 1, $fs: 1, $fb: 50%);
            }
        }
        
        // Vertikale skillelinjer
        .row .col_md_6:first-child,
        .row .col_md_4:first-child,
        .row .col_md_4:nth-child(2) {
            position: relative;
            
            &:after {
                position: absolute;
                top: 0;
                right: 0;
                @include rem('bottom', 35px);
                content: "";
                @include rem('border-right', 1px solid $alto);
            }
        }
        
        // Avstand nedover
        .ece_module_container {
            @include rem('margin-bottom', 42px);
        }
        
        // Større titler
        .ece_module_content_container {
            .ece_module_title {
                @include rem('font-size', 30px);
                line-height: ( 35px / 30px );

                .read_more_link {
                    // Samsvarer med read_more_link--30
                    @include rem('padding-right', 14px);
                    background-image: url("../images/blue_list_style_image_large.svg");
                    background-position: right 16px;
                }
            }
        }
        
        // Toppnyheter & Minisaker
        .toppsak,
        .teaser_small_container {
            .ece_module_container .ece_module_title {
                margin-top: 0;
            }
        }
        
        // Artikkelliste
        .ece_module_list .ece_module_container {
            @include rem('margin-bottom', 28px);
        }
    }
    
    
    // Justering minisaker og mediumsaker med bilder
    // ------------------------------------------
    .teaser_small_container .ece_module_container.has_image,
    .teaser_medium_container .ece_module_container.has_image {
    
        // Hide excerpt on small modules with image
        .ece_module_excerpt,
        .ece_module_meta,
        .ece_module_references {
            display: none;
        }
    
        // Mobil
        // Change to small images on mobile on small modules
        @media (max-width: $md_break_max) {
            @include flexbox();

            .ece_module_image_container {
                float: left;
                width: 48%;
                @include rem('margin', 0 14px 14px 0);
                
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex($fg: 1, $fs: 1, $fb: 40%);
            }
            
            .ece_module_content_container {
                // Make a floated div 100% height of its parent
                @include align-items();
                @include flex($fg: 1, $fs: 1, $fb: 60%);
            }
        }
    }
    // Hva skjer minisakbilde litt smalere på tablet for å få mer plass til tittel
    .teaser_small_container .ece_module_container.has_image {
        @media (min-width: $md_break) and (max-width: $lg_break_max) {
            .ece_module_image_container {
                @include flex($fg: 1, $fs: 1, $fb: 40%);
            }
        }
    }
    // Hva skjer mediumsaker litt mindre tittel på tablet
    .teaser_medium_container .ece_module_container .ece_module_title {
        @media (min-width: $md_break) and (max-width: $lg_break_max) {
            @include rem('font-size', 26px);
            line-height: ( 28px / 26px );
            
            .read_more_link {
                // Samsvarer med read_more_link--26
                @include rem('padding-right', 13px);
                background-image: url("../images/blue_list_style_image_large.svg");
                background-position: right 14px;
            }
        }
    }
}


// Module item without an image
// -------------------------
.toppsak .ece_module_container.no_image {
    text-align: center;
    @include rem('max-width', 900px);
    @include rem('padding', 21px 21px 7px);
    @include rem('border', 1px solid $zombie);
    @include box-sizing();
    @include border-radius();
    margin-left: auto;
    margin-right: auto;
    
    // reset flexbox
    display: block;
    
    .ece_module_content_container {
        margin-left: auto;
        margin-right: auto;
        
        // reset flexbox
        @include flex($fg: 1, $fs: 1, $fb: 100%);
    }
}


// Temaside
// --------------------------------------------
.ece_tema_grid {
    
    .ece_module_container {
        @include rem('padding-bottom', 6px);
        @include rem('border-bottom', 1px solid $zombie);
        @include rem('margin-bottom', 28px);
    }
    
    // Tablet & desktop
    @media (min-width: $md_break) {
        
        // Toppsak
        .toppsak .ece_module_container .ece_module_title {
            @include rem('font-size', 28px);
            line-height: ( 35px / 28px );
            @include rem('margin-bottom', 8px);

            .read_more_link {
                // Samsvarer med read_more_link--28
                @include rem('padding-right', 13px);
                background-image: url("../images/blue_list_style_image_large.svg");
                background-position: right 15px;
            }
        }
        
        // Ved siden av hverandre    
        .bunnsaker {
            @include clearfix();
            @include rem('border-bottom', 1px solid $zombie);
            @include rem('margin-bottom', 28px);
            
            .ece_module_container {
                padding-bottom: 0;
                border-bottom: 0 none;
                @include rem('margin-bottom', 14px);
            }
        }

        .venstrebunn {
            clear: left;
            float: left;
            margin-right: 2%;
        }

        .hoyrebunn {
            float: right;
            margin-left: 2%;
        }

        .venstrebunn, 
        .hoyrebunn {
            width: 48%;

            .sak {
                padding-bottom: 0;
                border-bottom: 0 none;
                margin-bottom: 0;
            }
        }
    }
}












