.file-container {
    @include rem('margin-bottom', 7px);
}
.file-link {
    display: inline-block;
    position: relative;
    @include rem('margin-left', -3px);
    @include rem('padding', 4px 4px 4px 33px);
    
    .file-icon {
        display: block;
        position: absolute;
        @include rem('width', 32px);
        @include rem('height', 32px);
        @include rem('top', 6px);
        @include rem('left', 3px);
        background: url(#{$image-path}/file.svg) no-repeat 0 0 transparent;
        
        &.pdf {
            background-image: url(#{$image-path}/pdf.svg);
        }        
        &.doc,
        &.docx,
        &.word {
            background-image: url(#{$image-path}/doc.svg);
        }
        &.ppt,
        &.pps,
        &.pptx,
        &.ppsx,
        &.powerpoint {
            background-image: url(#{$image-path}/ppt.svg);
        }
        &.xls,
        &.xlsx,
        &.excel {
            background-image: url(#{$image-path}/xls.svg);
        }
        &.htm,
        &.html {
            background-image: url(#{$image-path}/html.svg);
        }
        &.skjema {
            background-image: url($main_sprite_url);
            background-position: -167px -99px; // .application_icon
        }
    }
    
    .file-info {
        color: $emperor;
        text-transform: uppercase;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
    }

    &:hover,
    &:active {
        text-decoration: none;
        
        .file-title {
            text-decoration: underline;
        }
    }
}


// File / attachment list
// ---------------------------------------
.file-link-list {
	padding-left: 0;
	
	li {
		position: relative;
		@include rem('min-height', 49px);
		list-style: none;
		@include rem('border-bottom', 1px solid $janna);
		@include clearfix();
    
        .file-link {
            @include rem('padding-top', 13px);
            @include rem('padding-bottom', 14px);

            .file-icon {
                @include rem('top', 15px);
            }
        }
	}
    
    &--with-date {
        li {
            @include rem('padding-right', 90px);
    
            .date {
                position: absolute;
                right: 0;
                @include rem('top', 13px);
            }
        }
    }
}



