
// Layout
// --------------------------------------------------
  
// Add extra col-sizes if needed
  
// sm = small
// md = medium
// lg = large


@media (max-width: $md_break_max) {
  .smMa {
    @include rem('margin-left', 10px);
    @include rem('margin-right', 10px);
  }
  .smPa {
    @include rem('padding-left', 10px);
    @include rem('padding-right', 10px);
  }
  
  // Small grid system - for mobile only
  .row_sm {
    @include rem('margin-left', -10px);
    @include rem('margin-right', -10px);
    @include box-sizing();
    @include clearfix();
  }
  
  .col_sm_4, .col_sm_6, .col_sm_8, .col_sm_12 {
    float: left;
    @include rem('padding-left', 10px);
    @include rem('padding-right', 10px);
    @include rem('min-height', 1px);
    @include box-sizing();
  }
  
  // Column sizes
  .col_sm_4 {
    width: 33.3333333333%;
  }
  .col_sm_6 {
    width: 50%;
  }
  .col_sm_8 {
    width: 66.6666666666%;  
  }
  .col_sm_12 {
    width: 100%;
  }
}


@media (min-width: $md_break) {
  
  .col_md_3, .col_md_4, .col_md_6, .col_md_8, .col_md_9, .col_md_12,
  .col_lg_3, .col_lg_4, .col_lg_6, .col_lg_8, .col_lg_9, .col_lg_12 {
    float: left;
    @include rem('padding-left', 15px);
    @include rem('padding-right', 15px);
    @include rem('min-height', 1px);
    @include box-sizing();
    
    // Change the default left floating
    &.float_change {
      float: right;
    }
    
    // Align all extra column-boxes to the left. Example: projects on aktuelt-page
    &.clear_left {
      clear: left;
    }
  }
  
  .row {
    @include rem('margin-left', -15px);
    @include rem('margin-right', -15px);
    @include box-sizing();
    @include clearfix();
  }
  
  .row_no_clearfix {
    @include rem('margin-left', -15px);
    @include rem('margin-right', -15px);
    @include box-sizing();
  }
}

@media (min-width: $md_break) and (max-width: $lg_break_max) {
  
  // Column sizes
  .col_md_3 {
    width: 25%;
  }
  .col_md_4 {
    width: 33.3333333333%;
  }
  .col_md_6 {
    width: 50%;
  }
  .col_md_8 {
    width: 66.6666666666%;  
  }
  .col_md_9 {
    width: 75%; 
  }
  .col_md_10 {
    width: 83.3333333333%;
  }
  .col_md_12 {
    width: 100%;
  }
  
  // Offsets
  .col_md_offset_1 {
    margin-left: 8.3333333333%;
    margin-right: 8.3333333333%;
  }
  
  // If we list many col_md_6, every second needs to get a clear, starting with item 3
  .clear_col_md_6:nth-child(2n + 3) {
    clear: both;
  }
  
  // Lists with multicolumn (default is 2 columns) on tablet
  .md_multicolumn {
    @include multicolumn();
  }
}

@media (min-width: $lg_break) {
  
  // Column sizes
  .col_lg_3 {
    width: 25%;
  }
  .col_lg_4 {
    width: 33.3333333333%;
  }
  .col_lg_6 {
    width: 50%;
  }
  .col_lg_8 {
    width: 66.6666666666%;  
  }
  .col_lg_9 {
    width: 75%;
  }
  .col_lg_12 {
    width: 100%;
  }
  
  // Offsets
  .col_lg_offset_2 {
    margin-left: 16.6666666666%;
  }
  
  .col_lg_offset_4 {
    margin-left: 33.3333333333%;
  }
  
  // If we list many col_lg_6, every second needs to get a clear, starting with item 3
  .clear_col_lg_6:nth-child(2n + 3) {
    clear: both;
  }
  
  // If we list many col_lg_4, every third needs to get a clear, starting with item 4
  .clear_col_lg_4:nth-child(3n + 4) {
    clear: both;
  }
}





