// Paginering
// ----------------------------------------------------------
#paginering {
	padding-left: 0;
    clear: both;
    // background-color: $rum_swizzle;
    @include rem('padding', 7px 0 4px);
    @include rem('border-top', 1px solid $zombie);
    @include rem('border-bottom', 1px solid $zombie);
    
    li {
        list-style-image: none;
        display: inline-block;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        @include rem('margin', 0 3px 3px 0);

        &.current {
            background-color: $link_color;
            color: $white;
            text-align: center;
            @include SourceSansProRegular();
            @include box-sizing();
            @include rem('padding', 3px 5px);
            @include rem('min-width', 30px);
            @include border-radius();
        }
    
        a {
            display: block;
            text-align: center;
            @include box-sizing();
            @include rem('padding', 3px 5px);
            @include rem('min-width', 30px);
        }
    }
}
