
// Tjenester
// ---------------------------------------------------

.service_link_container {
  
  @media only screen and (max-width: $md_break_max) {
    .col_md_6 {
      width: 100%;    
    }
  }
}

// Sett equal heights on the link boxes
@media only screen and (min-width: $md_break) {
    
    // IMPORTANT
    // When using equal height container in combination with class=row
    // the second item will jump on a new line in Safari
    // because of the clearfix.
    // Use class=row_no_clearfix instead and it will work fine
    
    .equal_height_container {
      @include flexbox();
      @include flex-wrap(wrap);

      .equal_height_item {
        @include flexbox();
      }
    }
}


// Shortcut icons
// ---------------------------------------------------

.service_icon {
  float: left;
  display: block;
  background: url($service_sprite_url) no-repeat $white 64px 64px;
  @include rem('height', 64px);
  @include rem('width', 64px);
  
  &#skole-og-utdanning {
    background-position: 0 0;
  }
  &#barn-og-familie {
    background-position: -64px 0;
  }
  &#bolig-og-eiendom {
    background-position: -128px 0;
  }
  &#kultur-idrett-og-fritid {
    background-position: 0 -64px;
  }
  &#trafikk-reiser-og-samferdsel {
    background-position: -64px -64px;
  }
  &#helse {
    background-position: -128px -64px;
  }
  &#omsorg-trygd-og-sosiale-tjenester {
    background-position: 0 -128px;
  }
  &#natur-og-miljo {
    background-position: -64px -128px;
  }
  &#arbeid {
    background-position: -128px -128px;
  }
  &#rettslige-sporsmal {
    background-position: 0 -192px;
  }
  &#innvandring-og-integrering {
    background-position: -64px -192px;
  }
  &#forbrukersporsmal {
    background-position: -128px -192px;
  }
  &#individ-og-samfunn {
    background-position: 0 -256px;
  }
  &#skatter-og-avgifter {
    background-position: -64px -256px;
  }
  &#naring {
    background-position: -128px -256px;
  }
  
  // shortcut icons
  &.tools {
    background-position: -192px 0;
  }
  &.clock {
    background-position: -256px 0;
  }
  &.build {
    background-position: -320px 0;
  }
  &.school {
    background-position: -192px -64px;
  }
  &.child {
    background-position: -256px -64px;
  }
  &.map {
    background-position: -320px -64px;
  }
  &.shortcut {
    background-position: -192px -128px;
  }
  &.shaking_hands {
    background-position: -256px -128px;
  }
  &.sport {
    background-position: -320px -128px;
  }
  &.search {
    background-position: -192px -192px;
  }
}



.category_navigation_container {
  @include rem('margin-bottom', 120px);
  
  @media only screen and (min-width: $md_break) {
    @include rem('min-height', 130px);
    @include rem('margin-bottom', 60px);
  }
  
  @media only screen and (min-width: $lg_break) {
    @include rem('margin-bottom', 80px);
  }
}

// Tjenestekategori 1
// ---------------------------------------------------

.category_navigation_1 {
    padding: 0;
    margin: 0;

  li {
    list-style: none;
    @include rem('margin-bottom', 4px);
  }
  li a {
    position: relative;
    display: block;
    color: $black;
    position: relative;
    @include rem('font-size', 20px);
    line-height: ( 28px / 20px );
    @include rem('padding', 10px 44px 11px 14px);
    @include rem('border-left', 4px solid $main_red);
    background-color: $janna;
    @include hyphens();
      
    &:after {
      position: absolute;
      @include rem('top', 3px);
      @include rem('right', 15px);
      content: "";
      @include rem('width', 14px);
      @include rem('height', 32px);
      background: url(../images/list_arrows_red_down_double.svg) no-repeat 0 0 transparent;
    }
  }
  li.active > a,
  li.active > a:hover,
  li.active > a:active {
    color: $white;
    text-decoration: none;
    background-color: $main_red;
    @include rem('margin-bottom', 4px);
    
    &:after {
      background-image: url(../images/list_arrows_white_up_double.svg);
    }
  }
  @media only screen and (min-width: $md_break) {
    li a:after {
      background-image: url(../images/list_arrows_red_down_single.svg);
    }
    li.active > a:after,
    li.active > a:hover:after,
    li.active > a:active:after {
      background-image: url(../images/list_arrows_white_up_single.svg);
    }
  }
  
  @media only screen and (min-width: $lg_break) {
    
    & > li > a {
      border-left-color: $main_red;

      &:after {
        background-image: url(../images/list_arrows_red_right_double.svg);
      }
    }
      
    li.active > a {
      background: url(../images/tjenester_bg.svg) no-repeat center right $main_red;
      background-size: cover;
    }

    li.active > a:after,
    li.active > a:hover:after,
    li.active > a:active:after {
      display: none;
    }
    
  }
}


// Tjenestekategori 2
// ---------------------------------------------------

.category_navigation_2 {
  display: none;
  list-style: none;
  padding: 0;
  @include rem('margin-bottom', 10px);
  
  &.active {
    display: inherit;
  }

  li a {
    border-left: 0 none;
    background-color: $rum_swizzle;
    @include rem('padding-left', 16px);
    
    &:after {
      background-image: url(../images/list_arrows_red_down_single.svg);
    }
  }
  li.active > a,
  li.active > a:hover,
  li.active > a:active {
    color: $link_color;
    background-color: $rum_swizzle;
    @include rem('border-bottom', 1px solid $zombie);
  }

  li.active > a:after,
  li.active > a:hover:after,
  li.active > a:active:after {
    background-image: url(../images/list_arrows_blue_up_single.svg);
  }
  
  @media only screen and (min-width: $md_break) {
    
    li a:after {
      background-image: url(../images/list_arrows_red_right_single.svg);
    }
    
    li.active > a,
    li.active > a:hover,
    li.active > a:active {
      color: $white;
      background: url(../images/tjenester_bg.svg) no-repeat center right $main_red;
      background-size: cover;
      border-bottom: 0 none;
    }

    li.active > a:after,
    li.active > a:hover:after,
    li.active > a:active:after {
      display: none;
    }
  }
  
  @media only screen and (min-width: $lg_break) {
    & > li > a {
      @include rem('padding-left', 14px);
      @include rem('border-left', 4px solid $main_red);
    }
  }
}


// Tjenestekategori 3
// ---------------------------------------------------

.category_navigation_3 {
  display: none;
  list-style: none;
  @include rem('margin', 0 10px 6px 10px);
  padding: 0;
  
  &.active {
    display: inherit;
  }

  li {
    @include rem('border-bottom', 1px solid $zombie);

    &:last-child {
      border-bottom: 0 none;
    }
  }

  li a {
    background-color: transparent;
    @include border-radius();
    margin-bottom: 0;
    @include rem('padding-left', 8px);
    
    &:after {
      @include rem('right', 5px);
      background-image: url(../images/list_arrows_link.svg);
    }

    &:link,
    &:visited,
    &:active,
    &:focus,
    &:hover {
      color: $link_color;
    }

    &:hover,
    &:active {
      color: $link_color_hover;
      background-color: transparent;
      text-decoration: underline;
    }
  }
  
  @media only screen and (min-width: $md_break) {
    @include rem('border-top', 1px solid $zombie);
    @include rem('border-bottom', 1px solid $zombie);
    
    li a {
      @include rem('padding-left', 18px);
    
      &:after {
        @include rem('right', 14px);
      }
    }
  }
  @media only screen and (min-width: $lg_break) {
    border-top: 0 none;
    border-bottom: 0 none;
    
    li {
      border-bottom: 0 none;
      
      a {
        background-color: $white_sand;
        @include rem('padding-left', 38px);
        @include rem('padding-right', 12px);
    
        &:after {
          right: inherit;
          @include rem('left', 17px);
          background-image: url(../images/list_arrows_link.svg);
        }
          
        &:hover,
        &:active {
          background-color: $white_sand; // important to repeat that
        }
      }
    }
  }
}


// Layout changes on bigger screens
// ---------------------------------------------------

@media only screen and (max-width: $md_break_max) {
}
@media only screen and (min-width: $md_break) {
  .category_navigation_1 {
    position: relative;
    z-index: 10;
  }
  .category_navigation_3 {
    position: absolute;
    top: 0;
    left: 100%;
    width: 100%;
    @include rem('margin-left', 30px);
  }
}
@media only screen and (min-width: $md_break) and (max-width: $lg_break_max) {
  .category_navigation_2 li {
    position: relative;
  }  
}
@media only screen and (min-width: $lg_break) {
  .category_navigation_2 {
    position: absolute;
    top: 0;
    left: 100%;
    width: 100%;
    @include rem('margin-left', 30px);
    @include rem('min-height', 75px);
  }
}


// Tjenestekategori // Grey info boxes
// ---------------------------------------------------
@media only screen and (min-width: $md_break) {
  // Some min-heights for the infos
  .info_emneord_tjeneste,
  .info_tjeneste,
  .info_tjeneste > li > ul {
    
  }
  // Common for box and tekst
  .info_emneord_tjeneste:before,
  .info_emneord_tjeneste:after,
  .info_tjeneste > li > ul:before,
  .info_tjeneste > li > ul:after {
    position: absolute;
    left: 100%;
    width: 100%;
    height: 100%;
    @include rem('margin-left', 30px);
    @include box-sizing();
  }
  // Grey boks
  .info_emneord_tjeneste:before,
  .info_tjeneste > li > ul:before {
    top: 0;
    z-index: 0;
    content: "";
    background-color: $white_sand;
    @include rem('border', 1px solid $alto);
    @include border-radius();
    @include rem('min-height', 130px);
  }
  // Tekst
  .info_emneord_tjeneste:after,
  .info_tjeneste > li > ul:after {
    top: 25%;
    z-index: 1;
    text-align: center;
    @include rem('padding', 0 40px);
    content: "Velg kategori og underkategori til venstre for å se tjenester";
  }
}
@media only screen and (min-width: $lg_break) {
  // On bigger screens, change text
  .info_emneord_tjeneste:after {
    content: "Velg kategori til venstre for å se dens underkategorier";
  }
  .info_tjeneste > li > ul:after {
    content: "Velg underkategori til venstre for å se underliggende tjenester";
  }
  // On bigger screens, add empty box on the right
  .info_emneord_tjeneste > li:first-child:before {
    position: absolute;
    top: 0;
    left: 200%;
    z-index: 0;
    content: "";
    width: 100%;
    height: 100%;
    @include box-sizing();
    @include rem('margin-left', 60px);
    @include rem('border', 1px solid $alto);
    @include border-radius();
  }
}



// Tjenestebeskrivelse
// ---------------------------------------------------
.service_image_mobile {
    @include rem('margin-bottom', 28px);

    @media only screen and (min-width: $md_break) {
        float: right;
        @include rem('max-width', 340px);
        @include rem('margin', 4px 0 24px 30px);
    }
    @media only screen and (min-width: $lg_break) {
        display: none;
    }
}
.service_image_desktop {
    display: none;
    
    @media only screen and (min-width: $lg_break) {
        display: inherit;
        margin-top: 0;
        @include rem('margin', 2px 0 35px);
    }
}


.alternate_title {
    @include SourceSansProRegular();
    @include rem('margin', -7px 0 28px);
}

.service_description {
    @include rem('margin-bottom', 35px);
}

.service_content_max_width {
    @include rem('max-width', 600px);
}

// Edit notice and warning box
// -------------------------
.antall-tegn {
    text-align: right;
}
.antall-tegn-overskredet {
    color: red;
}




// Skjema accordion
// -------------------------
.accordion_item.important .accordion_content {
    @include rem('padding', 14px 12px);

    .application-link-list {
        margin: 0;

        li {
            @include rem('border-bottom', 1px solid $janna);
            margin-bottom: 0;

            &:last-child {
                border-bottom: 0 none;
            }

            a {
                display: block;
                @include rem('padding', 13px 10px 14px 28px);
                margin-bottom: 0;

                &:before {
                    @include rem('top', 12px);
                    @include rem('left', -7px);
                }
            }
        }
    }
}

