// The edit mode button
// -------------------------------------------------------------
.edit_mode_button_container {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 10;
    background-color: $white;
    @include rem('padding-left', 15px);

    @media only screen and (min-width: $md_break) {
        @include rem('padding-left', 30px);
    }
}

button.button--edit-mode,
button.button--edit-mode.active {
    color: $link_color;
    background-color: $white;
    @include rem('padding', 1px 10px 2px 26px);
    margin: 0;

    &:hover,
    &:active {
        color: $link_color;
        text-decoration: underline;
        background-color: #fdfdfd;
        border-color: $link_color;
        @include rem-box-shadow(0 2px 2px rgba($black, 8/100) inset);
    }

    @media (max-width: $md_break_max) {
        padding: 0;
        @include rem('height', 32px);
        @include rem('width', 32px);
    }
}

button.button--edit-mode {
    display: block;
    float: right;
    position: relative;
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    border-color: $alto;
    @include rem-box-shadow(0 1px 2px rgba($black, 6/100), 0 0 2px rgba($black, 8/100) inset);

    // offline_icon
    .icon {
        position: absolute;
        @include rem('top', 8px);
        @include rem('left', 8px);
        @include rem('height', 14px);
        @include rem('width', 14px);
        background-position: -169px -73px;
    }

    &.active {
        border-color: $danube;

        // online_icon
        .icon {
            background-position: -137px -73px;
        }
    }

    &[disabled] {
        color: $text_color;
        border-color: $grey7;
        background-color: $white;
        @include rem-box-shadow(0 1px 2px rgba($black, 6/100), 0 0 2px rgba($black, 8/100) inset);
        cursor: default;
        opacity: .35;
    }

    @media (max-width: $md_break_max) {
        .button--edit-mode__label {
            // screen-reader-text
            clip: rect(1px, 1px, 1px, 1px);
            height: 1px;
            width: 1px;
            overflow: hidden;
            position: absolute !important;
        }
    }
}