// Spesifikt for Leaflet kart som blir brukt under Fysioterapeuter med driftstilskudd

.leaflet-container,
.marker-cluster div,
.leaflet-popup-content,
.popup-header{
    color: $text_color;
    font-family: 'Source Sans Pro', Verdana, sans-serif;
    @include SourceSansProRegular();
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
}

.popup-header{
    @include rem('font-size', 21px);
}

.leaflet-popup-content{
    @include rem('margin', 15px 24px);
}

.leaflet-container a{
    color: $link_color;
}

.leaflet-container a:hover{
    color: $link_color_hover;
}

.leaflet-container a.leaflet-popup-close-button{
    @include rem('padding', 6px 6px 0 0);
    color: $grey7;
    @include SourceSansProRegular();
    @include rem('font-size', 24px);
}