#lightcase-case {
    text-shadow: none;
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    font-family: 'Source Sans Pro', Verdana, sans-serif;
    @include SourceSansProRegular();
}

#lightcase-info {
    background-color: $white;
    @include rem('padding-top', 9px);
    width: 100%;
    @include box-sizing();

    // Reset default transition / delay
    opacity: 1 !important;
    display: block !important;
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -ms-transition: none !important;
    -o-transition: none !important;
    transition: none !important;

    #lightcase-sequenceInfo,
    #lightcase-title,
    #lightcase-caption {
        color: $text_color;
    }

    #lightcase-sequenceInfo {
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
    }

    #lightcase-title {
        background: $white;
        @include rem('font-size', 19px);
        line-height: ( 28px / 19px );
    }

    #lightcase-caption {
        // screen-reader-text
        clip: rect(1px, 1px, 1px, 1px);
        height: 1px;
        width: 1px;
        overflow: hidden;
        position: absolute !important;
    }
}

#lightcase-nav a[class*="lightcase-icon-"],
.lightcase-icon-close {
    @include rem('font-size', 42px !important);
    background-color: $white;
    @include border-radius();
    text-decoration: none !important;

    &:before {
        color: $link_color;
    }

    &:hover::before {
        color: $link_color_hover;
        text-decoration: none;
        text-shadow: none;
    }

    &:focus{
        text-decoration: none;
    }
}

// Smaller scree sizes only
// --------------------------------------------------------
@media screen and (max-width: 640px) {

    #lightcase-overlay {
        background-color: $white;
    }

    #lightcase-info {
        position: static;
        width: 100%;
        padding: 0;
    }
    #lightcase-info #lightcase-sequenceInfo {
    }
    #lightcase-info #lightcase-title {
        top: 0;
        left: 0;
        padding: 0;
        position: relative;
        max-width: inherit;
    }
    #lightcase-nav a[class*="lightcase-icon-"] {
        @include rem('font-size', 32px !important);
    }
    #lightcase-nav a.lightcase-icon-close[class*="lightcase-icon-"] {
        position: fixed;
    }
}

a[class*="lightcase-icon-"] > span {
    @extend .screen-reader-text;
    text-indent: 0;
}

// Bigger screen sizes
// --------------------------------------------------------
@media screen and (min-width: 641px) {

    #lightcase-overlay {
        background-color: rgba($black, 36/100);
    }

    #lightcase-content {
        @include border-radius($base_border_radius $base_border_radius 0 0);
        @include box-shadow(none !important);


        & img{
            @include rem('border', 24px solid $white);
            border-bottom: 0 none;
        }
    }

    #lightcase-info {
        border-style: solid;
        border-color: $white;
        @include rem('border-width', 0 24px 22px);
        @include border-radius(0 0 $base_border_radius $base_border_radius);
    }

    #lightcase-nav a,
    .lightcase-icon-close{
        @include rem('border', 10px solid $white);
    }

    // Error
    #lightcase-case[data-lc-type=error] #lightcase-content {
        background-color: $white;
        @include box-sizing();
        @include rem('min-width', 260px);
    }

    #lightcase-case[data-lc-type=error] #lightcase-info {
        @include rem('min-width', 260px);
    }

    #lightcase-case p.lightcase-error {
        color: red;
        text-align: left;
        @include rem('width', 180px);
    }
}