// Bk 360
// ---------------------------------------------------


.bk360_body {
    @include rem('min-height', 300px);
}


// Filter search input
// ---------------------------
.bk360_filter_search_input {
    width: 100%;
    @include rem('max-width', 480px);
    @include rem('margin', 0 0 32px);
}

.bk360_filter_search_input_info {
    @include rem('margin', -24px 0 32px);
}

// Resultat overskrift
// ---------------------------
.bk360_result_list_header {
    @include rem('font-size', 32px);
    line-height: ( 35px / 32px );
    @include rem('margin', 0 0 24px);
}

// Medlemmer tabell - utvalgside
// ---------------------------
.bk360_members_table {

    .bk360_members_table_caption {
        height: 0;
        opacity: 0;
    }

    .div_table_row {
        @include box-sizing();
        @include clearfix();
        @include rem('padding-bottom', 12px);
        @include rem('margin-bottom', 9px);
        @include rem('border-bottom', 1px solid $janna);
    }

    .div_table_cell {
        float: left;
        @include rem('min-height', 1px);
        @include box-sizing();
    }

    .div_table_row_title {
        width: 100%;
    }

    .div_table_row_content {
        width: 100%;
        @include rem('padding-right', 10px);

        &:last-child {
            padding-right: 0;
        }
    }

    // Different number of cells
    &.with_3_table_cells .div_table_row_content {
        width: 50%;
    }
    &.with_4_table_cells .div_table_row_content {
        width: 33.33333333%;
    }

    .div_table_mobile_header {
        color: $emperor;
        display: block;
        @include rem('font-size', 16px);
        line-height: ( 21px / 16px );
    }

    .div_table_desktop_header_row {
        display: none;
    }

    .member_name {
        display: block;
        @include rem('padding', 9px 0);
    }

    @media only screen and (max-width: $md_break_max) {
        @include SourceSansProRegular();
    }

    @media only screen and (min-width: $md_break) {

        .div_table_row_title {
            width: 50%;
            @include rem('padding-right', 10px);
        }

        .div_table_row_content {
            width: 50%;
        }

        // Different number of cells
        &.with_3_table_cells .div_table_row_title {
            width: 56%;
        }
        &.with_3_table_cells .div_table_row_content {
            width: 22%;
        }
        &.with_4_table_cells .div_table_row_title {
            width: 34%;
        }
        &.with_4_table_cells .div_table_row_content {
            width: 22%;
        }

        .div_table_mobile_header {
            display: none;
        }

        .div_table_desktop_header_row {
            color: $grey7;
            display: block;
            @include box-sizing();
            @include clearfix();
            @include rem('font-size', 16px);
            line-height: ( 24px / 16px );
            @include rem('margin-bottom', 16px);
            @include rem('padding-bottom', 2px);
            @include rem('border-bottom', 1px solid $alto);
        }

        .div_table_row {
            @include rem('padding-bottom', 3px);
            @include rem('margin-bottom', 3px);

            .div_table_row_content {
                @include rem('padding-top', 9px);
            }
        }
    }
}