//Kemner betalingsskjema
.payment{
    &__kid{
        .form-element__number{
            @include rem('max-width', 350px);
            display: block;
        }

        & .payment__kid-text{
            color: $emperor;
            @include SourceSansProRegularItalic();
            @include rem('font-size', 17px);
            line-height: ( 21px / 17px );
            @include rem('margin-bottom', 7px);
        }

        .form-element__helptext--error{
            @include rem('margin-bottom', 21px);
        }
    }

    &__amount{
        .form-element__number{
            @include rem('max-width', 92px);
            @include rem('margin-right', 4px);
            text-align: right;
        }

        .form-element__textfield{
            @include rem('max-width', 52px);
            text-align: left;
        }
    }

    &__email{
        .form-element__textfield {
            @include rem('max-width', 530px);
        }
    }
}
