// Aktivitetsoversikt
// En Ruu-side som ikke lenkes til og som skjules fra soekebots // 10'17
// ---------------------------------------------------
.aktivitetsoversikt {
        
    .button_reset {
        background: none;
    	border: none;
    	color: $link_color;
    	margin: 0;
    	padding: 0;
    	text-align: left;
        	
    	&:hover {
        	color: $link_color_hover;
        	text-decoration: underline !important;
    	}
    }

    // Søk
    .case_search_container {
        position: relative;
        
        .clear_search_button {
        	background: none;
        	border: none;
        	@include box-shadow(none);
            @include rem('top', -39px);
            @include rem('right', 52px);
            @include rem('width', 38px);
            @include pseudo-icon($extend: ".svg-icon-small-grey-close-button",
            $position: 'after',
            $margin: 3px 4px 0,
            $opacity: 1);
        }
    }    
    
    // Utvalgspanel
    .aktivitetsvelger {
        padding: 0;
        
        li {
        	display: block;
        
            button.selected {
                @include SourceSansProBold();
            }
        }
    }    
    
    // Resultater
    .linkbox {
                
        .linkbox_extended_content {
            display: none;
        }
        
        &.active .linkbox_extended_content {
            display: inherit;
        }
        
        dl {        
            dt {
                float: left;
                @include rem('margin-right', 4px);
            }
            dd {
                margin-bottom: 0;
            }
        }
        
        .valgte_typer {
            color: $emperor;
            @include rem('font-size', 16px);
            line-height: ( 21px / 16px );
            
            span {
                @include rem('margin-right', 18px);
            }
        }
    }
}


