
// Ikoner paa toppen av kolonnemal
.sp_article_list_edit p {
    display: inline-block;
    width: 100%;
    @include rem('padding-bottom', 7px);
    @include rem('border-bottom', 1px solid $zombie);
}

// Ikoner ved escenic elementmal
.ece_module_container .sp_article_edit {
    a {
        img {
            display: inline-block;
            width: auto;
        }
    }
}

// Ikoner
    
.sp_edit_icon_link {
    position: relative;
    display: inline-block;
    text-align: center;
    @include border-radius();
    @include rem('width', 40px);
    @include rem('height', 38px);
    @include rem('margin', 0 6px 14px 0);
    @include rem('border', 1px solid $zombie);
    
    img {
        position: absolute;
        @include rem('top', 10px);
        left: 0;
        right: 0;
        margin: 0 auto;
    }
    
    &:hover,
    &:active {
        background-color: $rum_swizzle;
    }
}