// Organisasjonskart
// ---------------------------------------------------

.orgmap {
    
    &--level_1_header {
        @include rem('margin', 18px 0 16px);
    }
    
    &--level_1_list > li {
        list-style-image: none;
    }
    
    &--level_2_header {
        @include SourceSansProSemibold();
        @include rem('font-size', 21px);
        line-height: ( 28px / 21px );
        margin: 0;
    }
    
    &--level_2_list {
        @include rem('margin-bottom', 56px);
    }
    
    &--level_3_list {        
    }
}


// Avdelinger organisatorisk
// ---------------------------------------------------
.orglist {
    @include rem('padding-left', 4px);
    
    ul {
        @include rem('padding-left', 42px);
        
        ul {
            @include rem('padding-left', 14px);
        }
        
        li {
            position: relative;
            list-style-image: none;
        }
    }
    
    &--level_1_header {
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include SourceSansProRegular();
        @include rem('margin', 0 0 9px);
    }
    
    &--org_link {
        display: inline-block;
        @include rem('padding', 9px 0);
    }
    
    &--toggle {
        position: absolute;
        @include rem('top', 8px);
        @include rem('left', -42px);
        padding: 0;
        text-align: center;
        background-color: $white;
        @include rem('border', 1px solid $link_color);
        @include border-radius(3px);
        @include rem('width', 30px);
        @include rem('height', 30px);
        overflow: hidden;

        .toggle_icon {
            display: block;
            @include rem('font-size', 30px);
            line-height: ( 28px / 30px );
        }

        &.expanded .toggle_icon {
            @include rem('margin-top', -2px);
        }

        &:hover,
        &:active {
            background-color: $link_color;
            color: $white;
            text-decoration: none;
        }
    }
    
    &--level_1_list > li > .orglist--sub_level_list {
        background-color: $white_sand;
        @include border-radius();
        @include rem('margin', 7px 0 21px -42px);
        @include rem('padding', 3px 7px 11px 52px);
    }
    
    &--level_1_list > li > .orglist--sub_level_list > li > ul {
        background-color: $white;
        @include border-radius();
        @include rem('margin', 7px 3px 21px -42px);
        @include rem('padding', 3px 7px 11px 52px);
    }
    
    @media (min-width: $md_break) {
        
        ul ul {
            @include rem('padding-left', 32px);
        }
        
        &--level_1_list > li > .orglist--sub_level_list {
            @include rem('margin', 7px 0 21px -42px);
            @include rem('padding', 3px 7px 11px 74px);
        }

        &--level_1_list > li > .orglist--sub_level_list > li > ul {
            @include rem('margin', 7px 21px 21px -42px);
            @include rem('padding', 3px 7px 11px 74px);
        }
    }
}