.notice{
    position: relative;
    @include rem('padding', 63px 21px 7px);
    @include border-radius();
    @include box-sizing($boxmodel: content-box);

    &:before{
        content: "";
        position: absolute;
        @include rem('top', 11px);
        @include rem('left', 0px);
        transform: scale(0.4);
    }

    &--error{
        @include rem('border', 1px solid $brand_danger);
        @include pseudo-icon('.svg-icon-rednotice-60', $margin: 0 0 3px 1px);
    }


    &--approved{
        @include rem('border', 1px solid $brand_success);
        background-color: rgba($brand_success, 3/100);
        @include pseudo-icon('.svg-icon-greencheck-60', $margin: 0 0 3px 1px);
    }
    
    &--cancelled{
        @include rem('border', 1px solid $spindle);
        background-color: $water;
        @include pseudo-icon('.svg-icon-notice-60', $margin: 0 0 3px 1px);
    }

    &__content{
        @include clearfix();
        @include rem('max-width', 600px);
        @include rem('margin-bottom', 7px);

        h1, h2, h3, h4, h5, h6 {
            margin-top: 0;
        }

        a {
            text-decoration: underline;
        }
    }

    @media (min-width: $md_break) {
        @include rem('padding-top', 21px);
        @include rem('padding-left', 116px);
        @include rem('min-height', 88px);
        margin-top: 0;

        &:before {
            @include rem('top', 25px);
            @include rem('left', 26px);
            transform: scale(1);
        }

        .notice__content{
            p, ul {
                @include rem('margin-bottom', 14px);
            }
        }
    }
}
