$loader-padding: 40px !default;
$loader-size: 40px !default;
$loader-color: $link_color !default;

.loader {
  $animationDuration: 1.4s;
  $delayRange: 0.32s;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  &__animation{
    width: $loader-size * 2;
  }

  &__child {
    width: $loader-size / 2;
    height: $loader-size / 2;
    background-color: $loader-color;
    border-radius: 100%;
    display: inline-block;
    animation: loader $animationDuration ease-in-out 0s infinite both;

    &--1{
      animation-delay: -$delayRange;
    }

    &--2{
      animation-delay: -$delayRange / 2;
    }
  }

  &__text{
    text-align: center;
  }

}

@keyframes loader {
  0%, 80%, 100% {
    transform: scale(0);
  } 40% {
      transform: scale(1.0);
    }
}