.flex-table {
    width: 100%;
    
    &__title {
        color: $black;
        @include SourceSansProRegular();
        margin-top: 0;
        @include rem('border-bottom', 2px solid $zombie);
        @include rem('padding-bottom', 2px);
        
        a {
            color: $black;
        }

        &--results {
            border: none;
            color: $black;
            font-size: $font_size_h2;
        }
    }

    &__desktop-headerarea {
        display: none;
        width: 100%;
        @include rem('border-bottom', 2px solid $alto);
        @include rem('margin-bottom', 10px);
        @include rem('padding', 0 0 5px 0);
    }

    &__desktop-header {
        color: $grey7;
        @include rem('font-size', 17px);
        line-height: ( 21px / 17px );
        @include rem('padding-right', 10px);
        box-sizing: border-box;

        &--titleFirst {
            order: -99;
        }

        &--text-right{
            text-align: right;
        }

        &--fourfifth {
            min-width: 80%;
            width: 80%;
        }

        &--twothird {
            min-width: 67.7777777%;
            width: 67.7777777%;
        }

        &--onehalf {
            min-width: 50%;
            width: 50%;
        }

        &--onethird {
            min-width: 33.333333%;
            width: 33.333333%;
        }

        &--oneforth {
            min-width: 25%;
            width: 25%;
        }

        &--onefifth {
            min-width: 20%;
            width: 20%;
        }

        &--onesixth {
            min-width: 16.6666666%;
            width: 16.6666666%;
        }

        &--oneeight{
            min-width: 12.5%;
            width: 12.5%;
        }

        &--onetenth {
            min-width: 10%;
            width: 10%;
        }

        &--onetwenty {
            min-width: 5%;
            width: 5%;
        }
    }
    
    &__row {
        display: flex;
        flex-direction: column;
        @include rem('margin-bottom', 35px);

        &--nowrap {
            flex-direction: row;
            justify-content: space-between;
            @include rem('margin-bottom', 0);
            @include rem('border-bottom', 1px solid $almond);

            & .flex-table__cell {
                @include rem('padding', 13px 10px 12px 0);
            }
        }
        
        &--border-smallscreen {
        @include rem('margin-bottom', 0);
        @include rem('border-bottom', 1px solid $almond);

            & .flex-table__cell {
                @include rem('padding', 13px 10px 12px 0);
            }
        }

        &--border-bottom {
            @include rem('margin-bottom', 0);
            @include rem('padding', 10px 0);
            @include rem('border-bottom', 1px solid $alto);
        }
    }

    &__cell {
        display: flex;
        width: 100%;
        @include hyphens();

        &--title {
            order: -1;
            @include rem('border-bottom', 2px solid $zombie);
            @include rem('margin-bottom', 6px);
            @include rem('padding-bottom', 5px);
        }

        &--text-right {
            text-align: right;
            width: auto;
        }

        &--mobile-text-right {
            text-align: right;
            width: auto;
        }

        &--linkarrow {
            width: auto;

            &:before {
                background: url(../images/default_list_style_image.svg) no-repeat right top;
                content: '';
                width: 5px;
                @include rem('margin', 11px 5px 0 0);
            }
        }

        &--document-icon:before {
            background: url($main_sprite_url) no-repeat -160px -96px transparent; // .application_icon
            content: "";
            @include rem('width', 32px);
            @include rem('height', 32px);
            @include rem('margin', -2px 5px 0 0);
        }
    }
    
    &__cell-item {
        width: 100%;

        &--mobile-header {
            color: $grey7;
            width: 100%;
        }

        &--strong {
            @include SourceSansProSemibold();
        }

        &--srt {
            clip: rect(1px, 1px, 1px, 1px);
            height: 1px;
            overflow: hidden;
            position: absolute !important;
            width: 1px;
        }

        &--text-right {
            text-align: right;
        }

        &--mobile-text-right {
            text-align: right;
        }
    }

    &--column-border {

        .flex-table__desktop-header{
            @include rem('border-right', 1px solid $janna);
            @include rem('padding', 4px);
            font-size: $font_size_small;

            &:last-child {
                border: none;
            }
        }

        .flex-table__desktop-headerarea {
            justify-content: flex-start;
            padding: 0;
            margin: 0;
        }

        .flex-table__row {
            justify-content: flex-start;
        }

        .flex-table__cell {
            @include rem('padding', 0);
            @include hyphens();
            @include rem('font-size', 17px);
            @include rem('line-height', 28px);
        }

        .flex-table__cell:last-child {
            border: none;
        }

        .flex-table__cell--title {
            @include rem('font-size', 19px);
            margin: 0;

            .flex-table__cell-item a {
                text-decoration: none;
                @include rem('font-size', 19px);
            }
        }

        .flex-table__cell-item a {
            text-decoration: underline;
        }
    }

    &--postjournal {
        .flex-table__cell {
            @include rem('padding', 0);
        }
    }
    
    &--nowrap {

        & .flex-table__desktop-headerarea {
            display: flex;
            justify-content: space-between;
            margin-bottom: 0;
        }
    }
    
    // Tablet & desktop style
    @media only screen and (min-width: $md_break) {

        &__desktop-headerarea {
            display: flex;
            justify-content: space-between;
        }

        &__row {
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            @include rem('border-bottom', 1px solid $almond);
            margin: 0;

            &--border-bottom {
                @include rem('margin-bottom', 0);
                @include rem('padding', 0);
                @include rem('border-bottom', 1px solid $almond);
            }

            &--no-border {
                border: none;
            }

            &--double-border {
                @include rem('border-bottom', 2px solid $almond);
            }
        }
        
        &__cell {
            @include rem('padding', 13px 10px 12px 0);
            box-sizing: border-box;

            &--title {
                border: none;
                margin: 0;
                order: 0;
            }

            &--titleFirst {
                order: -1;
            }

            &--titleLast {
                order: 99;
            }

            &--fourfifth {
                min-width: 80%;
                width: 80%;
            }

            &--twothird {
                min-width: 67.7777777%;
                width: 67.7777777%;
            }

            &--onehalf {
                min-width: 50%;
                width: 50%;
            }

            &--onethird {
                min-width: 33.333333%;
                width: 33.333333%;
            }

            &--oneforth {
                min-width: 25%;
                width: 25%;
            }

            &--onefifth {
                min-width: 20%;
                width: 20%;
            }

            &--onesixth {
                min-width: 16.6666666%;
                width: 16.6666666%;
            }

            &--oneeight{
                min-width: 12.5%;
                width: 12.5%;
            }

            &--onetenth {
                min-width: 10%;
                width: 10%;
            }

            &--onetwenty {
                min-width: 5%;
                width: 5%;
            }

            &--mobile-text-right {
                text-align: left;
            }
        }

        &__cell-item {
            &--mobile-header {
                // screen-reader-text
                clip: rect(1px, 1px, 1px, 1px);
                height: 1px;
                width: 1px;
                overflow: hidden;
                position: absolute !important;
            }

            &--link-border {
                text-decoration: underline;
            }

            &--mobile-text-right {
                text-align: left;
            }
        }

        &--column-border {

            .flex-table__cell {
                @include rem('padding', 13px 4px 12px 4px);
                @include rem('border-right', 1px solid $janna);
            }

            .flex-table__cell--title {
                @include rem('font-size', 16px);
            }

            .flex-table__cell--title {
                @include rem('font-size', 17px);
                margin: 0;

                .flex-table__cell-item a {
                    text-decoration: underline;
                    @include rem('font-size', 17px);
                }
            }
        }
        
        &--postjournal {
            .flex-table__cell {
                @include rem('padding', 4px);
            }
        }
    }
}