// Mobile menu
// ---------------------------

.mob_menu_header {
    display: none;
}

@media (max-width: $lg_break_max) {
    
    .mob_menu_container {
        @include rem('border', 1px solid $zombie);
        border-top: 0 none;
        @include border-radius(0 0 $base_border_radius $base_border_radius);
        @include rem-box-shadow(0 1px 2px $alto);
        overflow: hidden;
    }
    
    .mob_menu_header {
        display: block;
        color: $link_color;
        @include rem('font-size', 20px);
        line-height: ( 28px / 20px );
        background-color: $rum_swizzle;
        @include rem('padding', 13px 50px 14px 10px);
        position: relative;
        
        &:hover,
        &:active {
            color: $link_color_hover;
            text-decoration: underline;
        }

        .icon {
            position: absolute;
            @include rem('top', 12px);
            @include rem('right', 7px);
            background-position: -256px -32px; // .arrow_up_19_blue

            .mob_menu_collapsed & {
                background-position: -256px 0; // .arrow_down_19_blue
            }
        }
    }
    
    // Toggle Effect
    .mob_menu_expanded .mob_menu_header {
        @include rem('border-bottom', 1px solid $almond);
        @include rem-box-shadow(0 1px 2px $alto);
    }
    
    .mob_menu {
        @include hyphens();
        padding: 0;
        margin: 0;
        @include transition($property: opacity);

        li {
            list-style: none;
            @include rem('border-bottom', 1px solid $almond);

            .menu_link {
                display: block;
                @include rem('padding', 10px 0 10px 10px);

                &:hover,
                &:active {
                    background-color: $rum_swizzle;
                    text-decoration: none;
                }
                &:focus {
                    position: relative;
                    z-index: 100;
                }
            }

            &.active > a {
                @include SourceSansProBold();
            }
        }
    }
    
    // Toggle Effect
    .mob_menu_collapsed .mob_menu {
        height: 0;
        opacity: 0;
    }
}