// Frontpage news
// --------------------------------------------
.frontpage_news {
    
    @media (min-width: $md_break) {
        @include rem('margin-top', 7px);
    }
    
    // Module header
    // -----------------
    .module_header.frontpage_news_header {
        @include rem('margin-bottom', 14px);
        
        @media (min-width: $md_break) {
            @include rem('margin', 16px 0 0);
        }
    }
    
    // Custom module box style
    // -----------------
    .module_box_style {
        display: block;
        width: 100%;
        border: 0 none;
        
        @media (min-width: $md_break) {
            margin-bottom: 0;
        }
    }
    
    // Promo content
    // -----------------    
    .frontpage_news_promo {
        @include clearfix();
        
        img {
            display: block;
            width: 100%;
        }

        .promo_title {
            @include rem('margin', 14px 0);
            @include rem('padding-left', 12px);
            @include SourceSansProRegular();
            @include rem('font-size', 19px);
            line-height: ( 28px / 19px );
            background: transparent url("../images/default_list_style_image.svg") no-repeat 1px 9px;
            @include hyphens($mode: manual);
            
            @media (min-width: $md_break) {
                @include rem('font-size', 22px);
                line-height: ( 28px / 22px );
                background-position: 1px 10px;
            }
        }

        // Ingen bilde
        &.no_image {
            color: $text_color;
            
            .promo_title {
                color: $link_color;
                @include rem('font-size', 28px);
                line-height: ( 35px / 28px );
                @include rem('padding-left', 15px);
                background: url(../images/default_list_style_image_large.svg) no-repeat transparent;
                @include rem('background-position', 3px 12px);
                @include rem('margin', 3px 0 7px);
                
                @media (min-width: $md_break) {
                    @include rem('font-size', 32px);
                    line-height: ( 35px / 32px );
                    @include rem('margin', 3px 0 5px);
                }
            }
            
            .promo_excerpt {
                @include rem('margin-bottom', 14px);
            }
            
            &:hover,
            &:active {
                text-decoration: none;

                .promo_title {
                    color: $link_color_hover;
                    text-decoration: underline;
                }
            }
            
            @media (min-width: $md_break) {
                @include rem('min-height', 250px);
            }
        }
    }
    
    // News list
    // -----------------
    .frontpage_news_link_list {

        .frontpage_news_list_header {
            margin: 0;
            @include SourceSansProRegular();
            @include rem('font-size', 19px);
            line-height: ( 28px / 19px );
        }
    }
}









